.slide-principal.desktop {

    .slick-track {
        height: auto;

        img {
            width: 100%;
            height: auto;
            margin: 0 auto;
        }
    }
}

.slide-principal.mobile {

    img {
        width: 100%;
        height: auto;
        margin: 0 auto !important;
    }
}

.combo-banner {

    position: relative;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    position: relative;
    margin-top: 60px;
    margin-bottom: 60px;

    @media screen and (max-width: 991px) {
        margin-top: 5px;
        margin-bottom: 16px;
    }

    &__miolo {
        cursor: pointer;
        position: relative;
        width: 32.7%;

        &:hover::before {
            content: '';
            position: absolute;
            width: 98%;
            height: 98%;
            background-color: RGBA(0, 0, 0, .3);
            top: 5px;
            left: 5px;
            pointer-events: none;
        }

        img {
            max-width: 100%;
            height: auto;
            margin: 0 auto;
        }
    }

    &__hover {
        display: none;
        position: absolute;
        overflow: hidden;
        z-index: 2;

        img {
            max-width: 100%;
            height: auto;
        }

        @media screen and (max-width: 1400px) {
            &.container-center {
                width: 100%;
            }
        }
    }

    // &__hover.ativo {
    //     display: block;
    //     position: absolute;
    //     background-color: #FFF;
    //     opacity: 1;
    //     top: 0;
    //     left: 0;
    //     z-index: 1;
    // }
}

.box-mosaico {
    margin-top: 60px;
    margin-bottom: 60px;
    min-height: 535px;
    max-height: 1000px;
    background-image: url(/arquivos/textura-mosaico.png);
    background-size: cover;
    display: flex;
    flex-direction: row;

    @media screen and (max-width: 991px) {
        margin-top: 89px;
        margin-bottom: 66px;
    }

    &__row {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        text-align: center;

        .box-banner {

            img {
                max-width: 97%;
                height: auto;
            }
        }
    }

    @media screen and (max-width: 991px) {
        min-height: unset;
        max-height: unset;
        height: auto;
        display: block;
    }
}

.box-linhas {
    margin-top: 20px;
    margin-bottom: 60px;
    display: flex;
    flex-direction: row;
    position: relative;
    width: 100%;
    height: 748px;

    @media screen and (max-width: 991px) {
        display: block;
        margin-top: 30px;
        margin-bottom: 75px;
    }

    &__overlayy {
        position: absolute;
        width: 0;
        height: 0;
        left: 0;
        top: 0;
        background-color: rgba(0, 0, 0, .5);

        @media screen and (max-width: 991px) {
            display: none !important;
        }
    }

    &__overlayy.ativo {
        width: 100%;
        height: 100%;

        @media screen and (max-width: 991px) {
            display: none !important;
        }
    }

    &__item {
        max-width: 25%;
        position: absolute;
        overflow: hidden;
        cursor: pointer;

        &:nth-child(1) {
            left: 0;
        }

        &:nth-child(3) {
            left: 25%;
        }

        &:nth-child(5) {
            left: 50%;
        }

        &:nth-child(7) {
            left: 75%;


            &.ativo {
                left: 60%;
            }
        }

        @media screen and (max-width: 991px) {
            max-width: 100%;
            position: static;
        }
    }



    &__item.ativo {
        max-width: 100%;
        z-index: 2;
        overflow: initial;

        a {
            display: flex;
        }

        &::after {
            content: "";
            background-color: rgba(0, 0, 0, 0.3);
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: block;
            pointer-events: none;
        }

        @media screen and (max-width: 991px) {
            display: none;
        }
    }

    &__item.ativo &__texto {
        height: 605px;
        opacity: 1;

        @media screen and (max-width: 991px) {
            display: none;
        }
    }

    &__item.ativo &__conheca {
        right: 250px;

        @media screen and (max-width: 991px) {
            right: 0;
            left: 20px;
        }
    }

    &__texto {
        position: absolute;
        width: 340px;
        height: 0;
        opacity: 0;
        margin: 0 auto;
        top: 50px;
        left: 100px;
        z-index: 2;
        pointer-events: none;

        @media screen and (max-width: 991px) {
            display: none;
        }
    }

    &__titulo {
        font-family: $secondFont;
        font-size: 80px;
        font-weight: 300;

        &--azul {
            color: #3dc1c6;
        }

        &--amarelo {
            color: #f2d482;
        }

        &--mostarda {
            color: #e3881b;
        }

        &--bege {
            color: #fdf2e8;
        }
    }

    &__descricao {
        color: $branco;
        font-family: $thirdFont;
        font-size: 17px;
        text-align: justify;
        font-weight: 400;
        line-height: 24.48px;
        width: 290px;
        margin: 0 auto;
    }

    &__conheca {
        position: absolute;
        bottom: 100px;
        right: 60px;
        text-transform: uppercase;
        color: $branco;
        cursor: pointer;
        pointer-events: none;

        @media screen and (max-width: 991px) {
            right: 0;
            left: 20px;
            position: relative;
        }

        &--azul::after {
            content: '';
            position: absolute;
            top: 0;
            right: -25px;
            width: 0;
            height: 0;
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
            border-left: 20px solid #3dc1c6;
        }

        &--amarelo::after {
            content: '';
            position: absolute;
            top: 0;
            right: -25px;
            width: 0;
            height: 0;
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
            border-left: 20px solid #f2d482;
        }

        &--mostarda::after {
            content: '';
            position: absolute;
            top: 0;
            right: -25px;
            width: 0;
            height: 0;
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
            border-left: 20px solid #e3881b;
        }

        &--bege::after {
            content: '';
            position: absolute;
            top: 0;
            right: -25px;
            width: 0;
            height: 0;
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
            border-left: 20px solid #fdf2e8;
        }
    }
}

