.page-erro {

    .bread {

        &__list {
            li:last-child {
                display: none;
            }
        }

        ul {

            li {

                a::after {
                    color: $branco;
                }
            }
        }

        &__list-item,
        li {
            font-weight: 700;
            font-size: 13px;
            color: $branco;

            a {
                font-size: 13px;
                font-weight: 400;

                &::after {
                    top: 2px;
                }
            }
        }
    }

    position: relative;
    height: 500px;

    @media screen and (max-width: 992px) {
        height: 600px;
    }

    &__banner {
        width: 100%;
        height: 496px;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;

        img {
            width: 100%;
        }

        @media screen and (max-width: 992px) {

            img {
                width: 100%;
                height: 160px;
            }

            background-color:#111118;
            height: 600px;
        }
    }

    &__dicas {
        clear: both;
    }

    &__title {
        color: #bab6b5;
        font-family: $secondFont;
        font-size: 83px;
        font-weight: 400;
        margin-bottom: 20px;

        @media screen and (max-width: 992px) {
            font-size: 50px;
            padding: 0 10px;
        }
    }

    &__text {
        color: #f7f7f7;
        font-family: $primaryFont;
        font-size: 13px;
        font-weight: 700;
        text-transform: uppercase;
        position: relative;
        margin-bottom: 30px;

        @media screen and (max-width: 992px) {
            padding: 0 10px;
        }

        &::before {
            content: '';
            position: absolute;
            width: 200%;
            height: 2px;
            background-color: $branco;
            bottom: -15px;
            left: -178%;

            @media screen and (max-width: 992px) {
                left: -120%;
            }
        }
    }

    &__lista {
        line-height: 30px;

        @media screen and (max-width: 992px) {
            padding: 0 10px;
        }
    }

    &__items {
        position: relative;
        color: $branco;
        font-size: 13px;
        padding-left: 20px;

        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 10px;
            border-left: 10px solid $branco;
            border-bottom: 5px solid transparent;
            border-top: 5px solid transparent;
        }

        &-last {
            color: #111317;
            font-family: $primaryFont;
            font-size: 13px;
            font-weight: 700;
            text-transform: uppercase;
            position: relative;
            margin: 40px 0;

            &::before {
                content: "";
                position: absolute;
                left: 8px;
                top: 16px;
                border-right: 7px solid #000;
                border-bottom: 4px solid transparent;
                border-top: 4px solid transparent;
            }
        }
    }

    &__tel,
    &__email {
        color: $branco;
        font-size: 13px;
    }

    &__button {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        width: 177px;
        font-size: 13px;
        padding-left: 15px;
        background-color: #bab6b5;

    }
}