.produto {
	.prateleira {
		&__btn-comprar {
			@media screen and (max-width: 991px) {
				a {
					font-size: 16px;

					@media screen and (max-width: 992px) {
						font-size: 12px;
					}
				}

				&::before {
					left: 5px;
				}
			}
		}
	}
}

.departamento {
	.prateleira {
		&__btn-comprar {
			@media screen and (max-width: 991px) {
				a {
					font-size: 16px;
				}

				&::before {
					left: 35px;
				}
			}
		}
	}
}

.prateleira {
	margin-top: 60px;

	@media screen and (max-width: 991px) {
		margin-top: 25px;
	}
}

.portobrasil-prateleira {
	margin-top: 60px;
}

.prateleira,
.portobrasil-prateleira {
	.slick-slide:not(.slick-center) {
		@media screen and (max-width: 991px) {
			opacity: 0.1;
		}
	}

	&__outOfStock {
		font-weight: 700;
	}

	&__title {
		color: #8a8c8d;
		font-size: 25px;
		font-family: $thirdFont;
		font-weight: 500;
		line-height: 24px;
		text-transform: uppercase;
		position: relative;
		margin-bottom: 50px;
		padding-left: 65px;

		b {
			color: #302e2b;
		}

		@media screen and (max-width: 991px) {
			padding-left: 0px;
			font-size: 16px;
		}

		@media screen and (min-width: 420px) {
			width: 80%;
			font-size: 19px;
			line-height: 22.92px;
		}
	}

	&__title::before {
		content: "";
		background-image: url(/arquivos/logo-sessao.png);
		position: absolute;
		width: 47px;
		height: 47px;
		top: -15px;
		left: 0;

		@media screen and (max-width: 991px) {
			display: none;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
		}
	}

	&__title::after {
		content: "";
		position: absolute;
		bottom: -20px;
		left: -61.5%;
		width: 100%;
		height: 3px;
		background-color: #232323;
		overflow: hidden;

		@media only screen and (min-width: 320px) and (max-width: 359px) {
			left: -3.5%;
		}

		@media only screen and (min-width: 360px) and (max-width: 375px) {
			left: -12.5%;
		}

		@media only screen and (min-width: 376px) and (max-width: 420px) {
			left: -16.5%;
		}
	}

	&__shelve-template {
		h2 {
			display: none;
		}

		.portobrasil-prateleira>ul {

			>li,
			.slick-slide>div>li {
				max-width: 100%;
				padding: 10px;
			}
		}
	}

	&__productImage {
		img {
			width: 325px;
			height: 325px;
			margin: 0 auto;
			border: 1px solid #eeeeee;

			@media screen and (max-width: 991px) {
				width: 100%;
				height: auto;
			}
		}
	}

	&__priceLabel {
		position: absolute;
		top: 5px;
		left: 5px;
		color: #8a8c8d;
		font-family: $thirdFont;
		font-size: 15px;
		font-weight: 700;
		line-height: 14.58px;
		text-transform: uppercase;
	}

	&__selosBlackFrete {
		visibility: hidden;

		.product-field {
			font-size: 0;

			.black-friday {
				position: absolute;
				padding: 0;
				right: 5px;
				top: 65px;
				width: 48px;
				height: 48px;
				background-color: #e5e5e5;
				color: #302e2b;
				font-family: $thirdFont;
				font-size: 10px;
				font-weight: 400;
				line-height: 14.58px;
				text-transform: uppercase;
				border-radius: 50%;
				display: flex;
				align-items: center;
				text-align: center;
				z-index: 4;
			}

			.frete-gratis {
				position: absolute;
				padding: 0;
				right: 5px;
				top: 10px;
				width: 48px;
				height: 48px;
				background-color: #302e2b;
				color: $branco;
				font-family: $thirdFont;
				font-size: 10px;
				font-weight: 400;
				line-height: 14.58px;
				text-transform: uppercase;
				border-radius: 50%;
				display: flex;
				align-items: center;
				text-align: center;
				z-index: 4;
			}
		}
	}

	.box-selo {
		position: relative;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		height: 35px;
		margin-bottom: 15px;
		display: none;

		@media screen and (max-width: 991px) {
			height: auto;
		}

		&__selo {
			font-family: $thirdFont;
			visibility: hidden;

			&--cinza {
				order: 1;
				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;
				background-color: #3e3e3e;
				width: 50%;
				height: 35px;

				@media screen and (max-width: 991px) {
					height: auto;
				}
			}

			&--azul {
				order: 2;
				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;
				background-color: #021c3f;
				width: 50%;
				height: 35px;

				@media screen and (max-width: 991px) {
					height: auto;
				}
			}
		}

		.product-field.product-field-type_1 {
			font-size: 0;

			ul {
				li {
					padding: 0;
					font-size: 12px !important;
					font-weight: 400;
					line-height: 13.13px;
					text-transform: uppercase;
					letter-spacing: 0.3px;
					color: $branco;

					@media screen and (max-width: 991px) {
						height: 35px;
						line-height: 35px;
					}
				}
			}
		}

		.novidade {
			font-size: 15px !important;
			font-weight: 400;
			line-height: 13.13px;
			text-transform: uppercase;
			letter-spacing: 0.3px;
			color: $branco;
		}

		.stoneware {
			font-size: 15px !important;
			font-weight: 400;
			line-height: 13.13px;
			text-transform: uppercase;
			letter-spacing: 0.3px;
			color: $branco;
		}
	}

	&__data {
		font-family: $thirdFont;
		// min-height: 160px;
		min-height: 50px;
		display: flex;
		flex-direction: column;
		position: relative;
		margin-top: 15px;
	}

	&__brandName {
		display: block !important;
		height: 24px;
		margin-bottom: 5px;

		@media screen and (max-width: 991px) {
			height: auto;
		}

		.product-field.product-field-type_6 {
			color: #949494;
			font-family: $fourFont;
			font-size: 15px;
			font-weight: 400;
			line-height: 24px;
			display: flex;
			flex-direction: row;
			align-items: baseline;

			@media screen and (max-width: 991px) {
				font-size: 12px;
			}

			ul {
				padding-left: 5px;

				li {
					color: #949494;
					font-family: $fourFont;
					font-size: 15px;
					font-weight: 400;
					line-height: 24px;
					width: 100%;
					margin: 0;

					@media screen and (max-width: 991px) {
						font-size: 12px;
					}
				}
			}
		}
	}

	&__classificacao {
		display: none;
		flex-direction: row;
		color: #545454;
		font-family: $thirdFont;
		font-size: 11px;
		font-weight: 400;
		line-height: 24px;
	}

	&__oldPrice {
		font-size: 10px;
		text-decoration: line-through;
		float: left;
		height: 25px;
		line-height: 25px;
		padding-right: 15px;
		color: #b1b1b1;

		b {
			color: #b1b1b1;
		}
	}

	&__lead {
		font-family: Montserrat;
		height: 40px;
		overflow: hidden;
		text-transform: uppercase;
		color: #545454;
		font-size: 17px;
		font-weight: 500;
		margin-bottom: 5px;

		a {
			font-family: Montserrat;
			font-size: 12px;
		}

		@media screen and (max-width: 1500px) {
			font-size: 14px;
		}

		@media screen and (max-width: 991px) {
			height: auto;
		}
	}

	&__bestPrice {
		display: block;
		width: 100%;
		color: #000000;
		font-family: $thirdFont;
		font-size: 21px;
		font-weight: 700;
		text-transform: uppercase;

		@media screen and (max-width: 1400px) {
			font-size: 14px;
		}

		@media screen and (max-width: 992px) {
			margin-bottom: 1px;
		}
	}

	&__installment {
		display: block;
		color: #484848;
		font-family: $thirdFont;
		font-size: 14px;
		font-weight: 400;
		font-style: normal;
		width: 100%;
		margin-top: 3px;

		@media screen and (max-width: 1400px) {
			font-size: 12px;
		}

		@media screen and (max-width: 992px) {
			font-size: 13px;
		}

		b {
			color: #000000;
			font-family: $thirdFont;
			font-weight: 700;
		}
	}

	&__btn-comprar {
		// border: 1px solid red;
		cursor: pointer;
		position: absolute;
		top: -10px;
		left: 0;
		width: 100%;
		height: 50px;
		color: $branco;
		font-weight: 600;
		font-family: $thirdFont;
		text-transform: uppercase;
		background-color: #3c3c3c;
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 2;
		opacity: 0;
		transition: opacity 0.5s;

		a {
			font-size: 12px;

			@media screen and (max-width: 992px) {
				font-size: 12px;
			}

			@media screen and (max-width: 340px) {
				display: block;
				width: 80%;
				text-align: right;
				font-size: 11px;
			}
		}

		&::before {
			content: "\e908";
			font-family: icomoon;
			font-size: 1rem;
			font-weight: 500;
			cursor: pointer;
			color: $branco;
			margin-right: 12px;

			@media screen and (max-width: 1366px) {
				left: 5px;
			}

			@media screen and (max-width: 992px) {
				left: 25px;
			}

			@media screen and (max-width: 400px) {
				left: 5px;
			}

			@media screen and (max-width: 360px) {
				font-size: 2rem;
			}

			@media screen and (max-width: 320px) {
				font-size: 1.5rem;
			}
		}

		@media screen and (max-width: 992px) {
			opacity: 1;
			position: initial;
			margin-top: 20px;
		}
	}
}

[layout="ded7544d-2c10-4152-ab56-81a0a0b9ca0b"]:hover {
	.prateleira__btn-comprar {
		opacity: 1;
	}
}

.prateleira__btn-comprar.hide-on-large-devices {
	display: flex !important;

	@media screen and (min-width: 1024px) {
		top: -100px;
	}
}

body.account-ing,
body.categoria-ing,
body.callcenter-ing,
body.compare-ing,
body.carrinho-abandonado-ing,
body.departamento-ing,
body.home-ing,
body.institucional-ing,
body.login-ing,
body.product-ing,
body.produto-ing,
body.resultado-busca-ing,
body.busca-vazia-ing,
body.account-esp,
body.callcenter-esp,
body.carrinho-abandonado-esp,
body.categoria-esp,
body.compare-esp,
body.departamento-esp,
body.home-esp,
body.institucional-esp,
body.login-esp,
body.product-esp,
body.produto-esp,
body.resultado-busca-esp,
body.busca-vazia-esp {

	.portobrasil-prateleira__outOfStock,
	.prateleira__outOfStock {
		display: none;
	}
}