.departamento {
    .instafeed {
        display: none !important;
    }

    .bread__nav {
        height: 40px;
    }

    .slick-list {
        width: 100% !important;
    }

    .slick-arrow {
        position: absolute;
        top: 50% !important;
        transform: translateY(-50%);
    }

    .slick-prev.slick-arrow {
        left: 25px !important;
        z-index: 1;
        border: 0;
        font-size: 0;
        width: 52px;
        height: 52px;
        background-color: hsla(0, 0%, 91.8%, .5);
        opacity: .8;
        border-radius: 50%;
        cursor: pointer;
    }

    .slick-next.slick-arrow {
        right: 25px !important;
        z-index: 1;
        border: 0;
        font-size: 0;
        width: 52px;
        height: 52px;
        background-color: hsla(0, 0%, 91.8%, .5);
        opacity: .8;
        border-radius: 50%;
        cursor: pointer;
    }

    .box-banner {
        img {
            width: 100% !important;
            height: auto !important;
        }
    }
}

@media only screen and (min-width: 320px) and (max-width: 780px) {
    .departamento-content__banner-deskSlick {
        display: none;
    }
}

@media only screen and (min-width: 992px) and (max-width: 3840px) {
    .departamento-content__banner-mobileSlick {
        display: none;
    }
}

.navigationItens {
    .row {
        .slickCategoria {
            margin-top: 15px;

            a {
                display: flex !important;
                justify-content: center;
                align-items: center;

                svg {
                    margin: 10px;
                    transition: .5s;

                    &:hover {
                        color: $preto;
                    }
                }
            }
        }
    }

    .slick-list {
        width: 100% !important;
        margin-left: -270px;

        @media only screen and (min-width: 320px) and (max-width: 780px) {
            width: 100% !important;
            margin-left: 0 !important;
        }
    }

    .slick-dots {
        margin-top: 40px;
    }
}

.department-container {
    clear: both;
    display: flex;
    flex-direction: column;

    .filters {

        &__nav {
            display: none !important;
        }

        &__navegacao {
            display: flex;
            flex-direction: row;
            align-items: center;
            height: 102px;
            background-color: $branco;
            position: relative;

            @media only screen and (min-width: 320px) and (max-width: 767px) {
                flex-wrap: wrap;
                height: 100%;
            }

            &::before {
                content: '';
                position: absolute;
                width: 100%;
                height: 102px;
                background-color: $branco;
                left: -50%;
                top: 0;
                z-index: -1;

                @media only screen and (min-width: 320px) and (max-width: 992px) {
                    content: none;
                }
            }

            &::after {
                content: '';
                position: absolute;
                width: 100%;
                height: 102px;
                background-color: $branco;
                right: -50%;
                top: 0;
                z-index: -1;

                @media only screen and (min-width: 320px) and (max-width: 992px) {
                    content: none;
                }
            }

            h2 {
                width: 9%;
                color: #3e3e3e;
                font-size: 15px;
                font-weight: 700;
                line-height: 13.13px;
                text-transform: uppercase;
                letter-spacing: 0.3px;
                font-family: $thirdFont;

                @media only screen and (min-width: 320px) and (max-width: 767px) {
                    width: 100%;
                    display: block;
                    text-align: center;
                    margin: 20px 0;
                }
            }
        }

        &__opcoes {
            width: 91%;
            display: flex;

            @media only screen and (min-width: 320px) and (max-width: 767px) {
                width: 100%;
                flex-direction: row;
                flex-wrap: wrap;
                justify-content: center;
                padding: 10px;
            }
        }

        &__categorias,
        &__colecoes,
        &__cores {
            position: relative;
            cursor: pointer;
            width: 200px;
            height: 40px;
            background-color: #8a8c8d;
            font-family: $thirdFont;
            font-size: 12px;
            font-weight: 400;
            line-height: 13.13px;
            text-transform: uppercase;
            text-indent: 15px;
            letter-spacing: 0.3px;
            display: flex;
            align-items: center;
            margin: 0 20px 0 0;

            @media only screen and (min-width: 320px) and (max-width: 767px) {
                margin: 5px 0;
                width: 50%;
            }

            &-text {
                color: $branco;
            }

            &-list {
                display: none;
                position: absolute;
                background-color: #8a8c8d;
                line-height: 30px;
                text-indent: 0;
                top: 100%;
                left: 0;
                width: 100%;
                padding: 5px 15px;
                color: $branco;

                .filtro-ativo {
                    color: $branco;
                }
            }

            &-list.ativo {
                display: block;
                z-index: 3;
            }

            &-item {
                color: $branco;
                font-size: 12px;
            }
        }

        &__ordenar {
            cursor: pointer;
            position: relative;
            display: flex;
            align-items: center;
            width: 185px;
            height: 40px;
            background-color: #8a8c8d;
            margin-left: auto;

            @media only screen and (min-width: 320px) and (max-width: 767px) {
                position: relative;
                width: 50%;
                margin: 5px 0;
            }

            &-text {
                font-family: $thirdFont;
                font-size: 12px;
                font-weight: 400;
                line-height: 13.13px;
                text-transform: uppercase;
                text-indent: 15px;
                color: $branco;
            }
        }

        &__select {
            color: $branco;
            font-family: $thirdFont;
            font-size: 12px;
            font-weight: 400;
            line-height: 13.13px;
            text-indent: 15px;
            letter-spacing: 0.3px;
            display: flex;
            align-items: center;
        }

        &__lista {
            display: none;
            position: absolute;
            background-color: #8a8c8d;
            text-transform: uppercase;
            line-height: 30px;
            text-indent: 0;
            top: 100%;
            left: 0;
            width: 100%;
            padding: 5px 15px;

            &-item {
                color: $branco;
                font-size: 12px;
            }
        }

        &__lista.ativo {
            display: block;
            z-index: 2;
        }

        &__link {
            color: $branco;

            &[data-href="OrderByPriceDESC"],
            &[data-href="OrderByPriceASC"],
            &[data-href="OrderByBestDiscountDESC"] {
                display: none !important;
            }
        }

        &__icon {
            position: absolute;
            display: block;
            top: 15px;
            right: 10px;

            &::before {
                content: '';
                position: absolute;
                right: 3px;
                top: 1px;
                width: 2px;
                height: 10px;
                background-color: #fff;
                transition: all .5s;
            }

            &::after {
                content: '';
                position: absolute;
                top: 1px;
                right: 3px;
                background-color: #fff;
                width: 2px;
                height: 10px;
                transform: rotate(90deg);
            }
        }

        &__icon.ativo {

            &::before {
                content: '';
                position: absolute;
                right: 3px;
                top: 1px;
                width: 2px;
                height: 10px;
                background-color: #fff;
                transform: rotate(90deg);
            }
        }
    }
}

.departamento-content {
    display: flex;
    flex-direction: column;

    &__text_wrapper {
        display: none;
    }

    .titulo-sessao {
        display: none;
    }

    &__vitrine {

        margin: 28px 0;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 991px) {
            margin-bottom: 0px !important;
        }

        .resultado-busca-numero {

            .label {
                display: none;
            }

            .value {
                position: relative;
                display: block;
                font-family: $thirdFont;
                color: #3e3e3e;
                font-size: 13px;
                font-style: italic;

                @media only screen and (min-width: 320px) and (max-width: 767px) {
                    display: none;
                }

                &::after {
                    content: 'Products found';
                    position: absolute;
                    right: -5px;
                    top: 4px;
                    width: 98%;
                    color: #3e3e3e;
                    font-family: $thirdFont;
                    font-size: 13px;
                    line-height: 13.13px;
                    letter-spacing: 0.3px;

                    @media only screen and (min-width: 320px) and (max-width: 767px) {
                        right: -25px;
                    }
                }
            }
        }

        .resultado-busca-termo,
        .resultado-busca-tempo {
            display: none;
        }

        .sub {
            display: none;
        }

        .pager.top,
        .pager.bottom {
            display: none;
        }

        .portobrasil-prateleira {
            margin-top: 15px;

            >ul {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;

                >li {
                    width: 23%;
                    margin: 20px 10px;

                    @media only screen and (min-width: 320px) and (max-width: 767px) {
                        width: 100%;
                    }

                    .prateleira__productImage {

                        display: block;
                        line-height: 0;

                        img {
                            width: 100%;
                            min-height: auto;
                            margin: 0 auto;
                            border: 1px solid #eeeeee;

                            @media screen and (max-width: 991px) {
                                width: 100%;
                                height: auto;
                            }
                        }
                    }
                }
            }
        }

        .box-button {
            max-width: 100%;
            text-align: center;
            margin-top: 100px;
            margin-bottom: 60px;

            @media screen and (max-width: 991px) {
                margin-top: 20px;
                margin-bottom: 20px;
            }

            .btn-mais-produtos {
                width: 328px;
                height: 52px;
                text-transform: uppercase;
                color: $branco;
                background-color: #3e3e3e;
                cursor: pointer;
                position: relative;
                border: none;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: auto;

                @media only screen and (min-width: 320px) and (max-width: 767px) {
                    width: 80%;
                    margin: 20px auto;
                }

                &__icon {
                    content: "";
                    border-left: 5px solid transparent;
                    border-right: 5px solid transparent;
                    border-top: 10px solid #fff;
                    margin-left: 12px;
                }
            }
        }
    }
}

.quem-somos-departamento {

    @media screen and (max-width: 991px) {
        margin-top: 0px;
        padding-bottom: 20px;
    }
}

body.departamento-esp,
body.produto-esp {
    .departamento-content {
        &__vitrine {
            .resultado-busca-numero {
                .value {
                    &::after {
                        content: 'Productos encontrados';
                    }
                }
            }
        }
    }
}