.bread ul{
    width: 100%;
    float: left;
    list-style-type: none;
    padding: 20px 0;
    text-transform: uppercase;
}

.bread ul li a::after{
    content: '\e901';
    font-size: 12px;
    font-weight: 900;
    font-family: 'icomoon';
    transform: rotate(180deg);
    float: right;
    color: #000;
    margin: 0 10px;
    position: relative;
    top: 5px;
}

.bread ul li.last a::after{
    display: none;
}

.bread ul li a {
    font-size: 12px;
}

.bread ul li.last a{
    font-weight: bold;
}

.bread ul li{
    float: left;
}