/* Reset css */
* {
    margin: 0 auto;
    padding: 0 auto;
    color: #000;
    outline-color: transparent!important;
    scroll-behavior: smooth;
}
*:focus {
    outline-color: transparent!important;
}
*, *:before, *:after {
    margin: 0;
    padding: 0; 
    outline: 0;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    font-family: $primaryFont;
}
ul{
    margin: 0;
    padding: 0;
    list-style: none;
}
li{
    line-height: inherit;
    list-style: none;;
}
a{
    text-decoration: none;
    color: inherit;
}
h1,h2,h3,h4,h5,h6{
    font-size: inherit;
    margin: 0;
    padding: 0;
    line-height: initial;
}
p{
    padding: 0;
    margin: 0;
}
a:focus, a:active, 
button:focus, 
button:active, 
.btn:focus, 
.btn:active, 
.button:focus, 
.button:active {
    outline: none;
    text-decoration: none;
    color:inherit;
}
a:hover{
    text-decoration: none;
    color:inherit;
}
select,
textarea,
input[type="text"],
input[type="password"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="week"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="color"],
.uneditable-input {
    display: inline-block;
    padding: inherit;
    margin-bottom: inherit;
    font-size: inherit;
    line-height: inherit;
    color: inherit;
    vertical-align: middle;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
    height: 40px;
}
input[type="checkbox"],
input[type="radio"], 
select[type="checkbox"], 
select[type="radio"], 
textarea[type="checkbox"], 
textarea[type="radio"] {
    padding: 0;
}

body {
    width: 100%;
    padding: 0!important;
    overflow-x: hidden;
}
body.aberto {
    overflow: hidden;
}
body.aberto .topo .menu-departamento {
    overflow: auto;
}

ul {
    margin: 0;
    padding: 0;
}

h3 {
    margin: 0;
    padding: 0;
}

@media only screen and (min-width: 320px) and (max-width: 991px) {

    header.desktop {
        height: 0;
        display: none;
    }

    header.desktop .container-center .header-principal .miniCart .miniCart__icon-cart {
        display: none;
    }

    header.desktop .eclipse.miniCart__qtd-cart {
        display: none;
    }

    .tipbar, .header-principal > div:not(.miniCart) {
        display: none!important;
    }

    .slide-principal.desktop {
        display: none;
    }

    .combo-banner.desktop {
        display: none;
    }

    .mobile {
        display: block;
    }
}
@media only screen and (min-width: 992px) and (max-width: 3840px) {

    .mobile {
        display: none;
    }
    .slide-principal.desktop {
        display: block;
    }

    header.desktop {
        display: block;
    }

    .desktop {
        display: flex;
    }
}

.container {
    width: 100%!important;
    padding: 0;
    margin: 0;
    display: block;
}

.container-center {
    max-width: 1360px;
    margin: 0 auto;
    display: block;

    @media screen and (max-width: 1400px) {                
        width: 95%;        
    }

    @media screen and (max-width: 768px) {
        width: 100%;
        padding: 0 5px;
    }
}

.hide-on-large-devices {
    display: none !important;
}

@media screen and (max-width: 991px) {
    .hide-on-large-devices {
        display: block !important;
    }

    .prateleira__btn-comprar.hide-on-large-devices {
        display: flex !important;
    }

    .hide-on-small-devices {
        display: none !important;
    }
}

.overlay {

    &-black {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0, 0.5);
        z-index: -1;
        opacity: 0;
        transition: opacity .5s ease-in-out;

        &.ativo {
            opacity: 1;
            z-index: 2;
        }
    }
}

a.ui-corner-all img {
    width: 5% !important;
    height: 35px!important;

    @media screen and (max-width: 992px) {
        width: 35% !important;
        height: auto;
    }
}

.ui-widget-content .ui-corner-all {
    width: 100%;
    color: #000;
    padding: 5px 0;
    float: left;
    text-transform: capitalize;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.ui-widget-content .ui-menu-item {    
    border-bottom: 1px solid #c1c1c1;
}

ul.ui-autocomplete.ui-menu.ui-widget.ui-widget-content.ui-corner-all {
    width: 700px;
    height: 365px;
    overflow-y: scroll;

    @media screen and (max-width: 992px) {
        width: 215px;
        height: 300px;
        overflow: scroll;
    }
}

.voltar-ao-top {
    width: 70px;
    height: 70px;
    box-shadow: 0 5px 16px rgba(0, 0, 0, 0.15);
    background-color: #8a8c8d;
    opacity: 0.7;
    position: absolute;
    bottom: 0;
    right: 30px;
    cursor: pointer;

    @media screen and (max-width: 992px) {
        right: 5px;
    }

    @media screen and (max-width: 1400px) {
        width: 50px;
        right: 5px;
    }
}

.voltar-ao-top:before {
    content: '\e905';
    font-family: icomoon;
    color: #fff;
    font-size: 30px;
    display: block;
    text-align: center;
    margin-top: 10px;
}

.voltar-ao-top:after {
    content: 'TO TOP';
    display: block;
    text-align: center;
    color: #fff;
    font-size: 12px;
}

div#returnToTop {
    display: none!important;
}


/* Inicio load */

body>#ajaxBusy.loading {
    position: fixed;
    right: 0;
    bottom: 0;
    width: 100px;
    height: 100px;
    margin: auto;
    display: block!important;
    flex-flow: row nowrap;
    justify-content: space-around;
    z-index: 999999;
    text-indent: -99999px;
    text-align: center;
    background-color: transparent;
}
body>#ajaxBusy.loading[style="display: none;"] {
    display: none!important;
}
body>#ajaxBusy.loading p {
    position: relative;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    border-style: solid;
    animation: loader 500ms linear infinite;
    border-width: 3px;
    border-color: #333 transparent;
    z-index: 9999;
    margin: -25px 0 0 -25px;
    top: 50%;
    left: 50%;
}
@keyframes loader {
    to {
        transform: rotate(360deg);
    }
}
@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@-moz-keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@-webkit-keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@-ms-keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@-o-keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

