.menu-desktop {

    &__nav {

        &-sub {
            &:hover .menu-desktop__nav-container {
                display: flex;
                flex-direction: column;
                min-width: 310px;
                max-width: 365px;
                line-height: 35px;
                padding: 40px;
                top: 22px;
                background-color: #fff;
                z-index: 3;
                overflow-x: hidden;
            }
        }

        &-nivel-1 {

            &-item:hover {
                border-bottom: 2px solid #333;
            }

            &-item {
                font-size: 13px;
                color: #302e2b;
                position: relative;

                &--title {
                    font-weight: bold;
                    font-size: 18px;
                    position: relative;
                    background-color: $branco;
                    padding-right: 15px;
                    white-space: nowrap;
                    width: max-content;
                }

                &--ver-mais {
                    display: none;
                }

            }

            &-title {
                font-size: calculadora-rem(13);
                font-weight: 400;
                height: 50px;
                line-height: 50px;
            }
        }

        &-container {
            display: none;
            position: absolute;

            &-sub {
                text-align: left;
            }
        }
    }

    &__nav-manual {

        &-sub {
            &:hover .menu-desktop__nav-manual-container {
                display: flex;
                flex-direction: column;
                min-width: 310px;
                max-width: 365px;
                line-height: 35px;
                padding: 30px;
                top: 60px;
                background-color: #fff;
                z-index: 3;
                overflow-x: hidden;
            }
        }

        &-nivel-1 {

            &-item:hover {
                border-bottom: 2px solid #333;
            }

            &-item {
                font-size: 13px;
                color: #302e2b;
                position: relative;

                &--title {
                    font-weight: bold;
                    font-size: 18px;
                    position: relative;
                    background-color: $branco;
                    padding-right: 15px;
                    white-space: nowrap;
                    width: max-content;
                }

                &--ver-mais {
                    display: none;
                }

            }

            &-title {
                font-size: calculadora-rem(13);
                font-weight: 400;
                height: 50px;
                line-height: 50px;
            }
        }

        &-container {
            display: none;
            position: absolute;

            &-sub {
                text-align: left;
            }
        }
    }
}