.popUp {
    position: fixed;
    top: 150px;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    z-index: 3;

    @media only screen and (max-width: 1400px) {
        top: 10px;
    }
}

.popUp__container {
    background-color: $branco;
    width: 485px;
    padding: 70px 20px 20px 20px;
    position: relative;
    @media only screen and (max-width: 1400px) {
        top: 10px;
        height: 475px;
        width: 450px;
        padding: 60px 20px 20px;
    }
}

.popUp__close {
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: 30px;
    cursor: pointer;

    @media only screen and (max-width: 1400px) {
        top: 15px;
        font-size: 20px;
    }
}

.popUp__title {
    display: block;
    width: 100%;
    text-align: center;

    @media only screen and (max-width: 1400px) {
        margin: 20px 0 0;
    }    
}

.popUp__porcentagem {
    display:block;
    font-weight: 400;
    text-align: center;
    color: #2D2A26;
}

.popUp__info {
    display: block;
    text-align: center;
}

.popUp__porcentagem strong {
    font-size: 55px;
    color: #2D2A26;

    @media only screen and (max-width: 1400px) {
        font-size: 45px;
    }
}

.popUp__newsletter {
    margin-top: 30px;

    .newsletter__box--sent {
        color: #2D2A26;
    }
}

.popUp__name {
    width: 100%;
    height: 47px !important;
    text-align: center;
    margin-bottom: 33px !important;
    border: 1px solid black;
    @media only screen and (max-width: 1400px) {
        height: 40px;
    }
}

.popUp__mail {
    width: 100%;
    height: 47px !important;
    border: 1px solid black;
    text-align: center;
    @media only screen and (max-width: 1400px) {
        height: 40px;
    }
}

.popUp__button-newsletter {
    display: block;
    background-color: #302e2b;
    color: #FFF;
    width: 60%;
    height: 55px;
    text-transform: uppercase;
    margin: 20px auto;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    text-align: center;
    line-height: 55px;

    &:hover {
        color: #FFF;
    }

    @media only screen and (max-width: 1400px) {
        height: 40px;
        font-size: 15px;
    }
}

.popUp__no-thanks {
    display: block;
    text-align: center;
    text-transform: uppercase;
    text-decoration: underline;
    margin: 30px auto;
    cursor: pointer;
}