.barrinha {
    background-image: url('/arquivos/barrinha-textura.png');
    background-repeat: no-repeat;
    height: 9px;
    width: 100%;
    display: none;
}

.quem-somos,
.quem-somos-departamento {

    padding-bottom: 60px;
    // background-image: url('/arquivos/Textura-Quem_somos.png');
    background-repeat: no-repeat;

    .container-center {

        text-align: center;

        .titulo-quemSomos {
            display: none;
        }

        .titulo-quemSomos,
        .titulo-sessao {
            font-family: $secondFont;
            font-size: 80px;
            font-weight: 300;
            margin-bottom: 20px;

            @media screen and (max-width: 768px) {
                font-size: 48px;
            }
        }

        p {
            font-size: 12px;
            color: #2d2926;
            line-height: 29.17px;
            letter-spacing: 0.25px;
            margin-bottom: 50px;
            text-align: justify;
            &:first-of-type{
                margin-bottom: 15px;
            }

            @media screen and (max-width: 768px) {
                padding: 0 25px;
                color: #2d2926;
                font-family: Montserrat;
                font-size: 12px;
                font-weight: 300;
                text-align: left;

                strong {
                    font-weight: 700;
                }
            }
        }

        .conhecaMais {
            position: relative;
            display: block;
            width: 300px;
            height: 50px;
            line-height: 50px;
            background-color: #302e2b;
            color: $branco;
            text-transform: uppercase;
            cursor: pointer;
            text-align: center;
            margin: auto;

            a {
                display: flex;
                align-items: center;
                justify-content: center;
                font-family: Montserrat;
                font-size: 12px;
                font-weight: 500;
                text-transform: uppercase;

                &::after {
                    content: '\e901';
                    font-family: 'icomoon';
                    font-size: 20px;
                    transform: rotate(180deg);
                    margin-left: 5px;

                    @media screen and (max-width: 768px) {
                        font-size: 13px;
                    }
                }
            }

            @media screen and (max-width: 768px) {
                width: 70%;
                font-size: 13px;
            }
        }


    }
}

.instafeed {
    padding-bottom: 87px;
    position: relative;
    display: flex;
    justify-content: center;
    // background-image: url('/arquivos/Textura-Quem_somos.png');
    background-repeat: no-repeat;

    @media screen and (max-width: 992px) {
        flex-direction: column;
        margin-bottom: 10px;
        padding-bottom: 0px;
    }

    &__informativo {
        position: absolute;
        top: 3%;
        width: 300px;
        height: 180px;
        min-height: 180px;
        background-color: #191e25;
        padding: 25px;

        @media screen and (max-width: 992px) {
            position: relative;
            background-color: transparent;
            padding: 0;
            width: 100%;
            min-height: 160px;
        }

        &::before {
            position: absolute;
            content: '';
            border: 3px solid #191e25;
            width: 340px;
            height: 230px;
            top: -25px;
            left: -20px;

            @media screen and (max-width: 992px) {
                content: none;
            }
        }
    }

    &__title {
        display: block;
        color: $branco;
        text-align: center;
        font-size: 17px;
        font-weight: 700;
        line-height: 23.11px;
        text-transform: uppercase;

        @media screen and (max-width: 992px) {
            background-color: #191e25;
            font-size: 19px;
            padding: 20px 0 0;
        }
    }

    &__nameUser {
        display: block;
        text-align: center;
        color: $branco;
        font-family: Montserrat;
        font-size: 13px;
        font-weight: 300;
        line-height: 23.11px;
        margin-bottom: 20px;

        @media screen and (max-width: 992px) {
            background-color: #191e25;
            margin: 0;
            padding-bottom: 20px;
        }
    }

    &__text {
        display: block;
        text-align: center;
        color: $branco;
        font-size: 13px;
        font-weight: 300;
        line-height: 23.11px;
        margin-bottom: 20px;

        @media screen and (max-width: 992px) {
            color: #191e25;
        }
    }

    &__hashtag {
        display: block;
        text-align: center;
        color: $branco;
        font-size: 13px;
        font-style: italic;
        font-weight: 300;
        line-height: 23.11px;

        @media screen and (max-width: 992px) {
            color: #191e25;
        }
    }

    #instafeed {
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    a {
        display: block;
        margin: 0 3px;
        text-align: center;
        width: calc(100% / 5);


        img {
            max-width: 100%;
            height: auto;
        }

        @media screen and(max-width: 992px) {
            display: none;
            width: 50%;
            margin: 0 auto;

            img {
                max-width: 100%;
                height: auto;
            }
        }
    }
}

.newsletter {

    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    padding-top: 40px;
    width: 100%;
    min-height: 250px;
    // background-image: url('/arquivos/Textura-Quem_somos.png');
    background-repeat: no-repeat;

    @media screen and(max-width: 992px) {
        padding-top: 0px;
    }

    &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 140px;
        background-color: rgba(45, 41, 38, .1);
        bottom: 0;
        z-index: 0;

        @media screen and (max-width: 768px) {
            display: none;
        }
    }

    &__wrapper {
        position: relative;
        background-color: #302e2b;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 180px;
        width: 1156px;
        bottom: 0;
        padding: 55px 40px 30px;
        z-index: 1;

        @media screen and (max-width: 768px) {
            width: 100%;
            padding: 30px 25px 31px 27px;
            height: 100%;
        }
    }

    &__titulo {
        position: absolute;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        text-transform: uppercase;
        font-weight: 500;
        font-size: 19px;
        letter-spacing: -0.33px;
        width: 370px;
        height: 50px;
        background-color: #8a8c8d;
        color: $branco;
        top: -25px;
        z-index: 2;

        @media screen and (max-width: 992px) {
            display: none;
        }
    }

    &__titulo-mobile {
        display: none;

        @media screen and (max-width: 992px) {
            display: block;
            color: $branco;
            font-size: 37px;
            font-weight: 500;
            text-transform: uppercase;
        }
    }

    &__texto {
        color: $branco;
        font-size: 17px;
        font-weight: 300;
        letter-spacing: -0.33px;
        margin-bottom: 20px;

        strong {
            color: $branco;
            font-weight: 500;
        }

        @media screen and (max-width: 768px) {
            text-align: center;
            font-size: 13px;
        }
    }

    &__form {
        display: flex;
        flex-direction: row;
        width: 100%;

        @media screen and (max-width: 768px) {
            flex-direction: column;
            position: relative;
        }
    }

    &__input {
        width: 460px;
        height: 48px !important;
        text-indent: 10px;
        color: #c3c3c3;
        border: none !important;

        &--name {
            width: 418px;
            margin-right: 25px;
        }

        &-name--error,
        &-email--error {
            border: 1px solid red !important;
            animation: error 200ms 2;
        }

        @media screen and (max-width: 768px) {
            width: 100%;
            margin: 5px 0;
            font-size: 13px;

            &:nth-child(2) {
                width: 70%;
                margin-top: 16px;
            }
        }
    }

    &__btn {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 700;
        font-size: 17px;
        text-transform: uppercase;
        background-color: #8a8c8d;
        color: $branco;
        width: 170px;
        height: 48px;
        border: none;

        &:hover {
            color: $branco;
        }

        &:focus {
            color: $branco;
        }

        &:visited {
            color: $branco;
        }

        @media screen and(max-width:768px) {
            font-size: 13px;
            position: absolute;
            right: 0;
            bottom: 0;
            width: 30%;
            z-index: 2;
        }
    }

    &__box--sent {
        color: $branco;
        width: 100%;
        text-align: center;
    }
}

.newsletter-product {

    background-color: #302e2b;

    @media screen and (max-width: 992px) {
        min-height: 290px;
        margin-top: 20px;
    }

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        max-width: 85%;
        height: 150px;
        margin: 0 auto;


        @media screen and (max-width: 1280px) {
            max-width: 95%;
        }

        @media screen and (max-width: 992px) {
            flex-direction: column;
            max-width: 100%;
        }
    }

    &__boxText {
        width: 481px;
        position: relative;

        &::after {
            content: '';
            position: absolute;
            width: 3px;
            height: 120px;
            background-color: $branco;
            right: 0;
            bottom: -15px;
        }

        @media screen and (max-width: 992px) {
            width: 100%;

            &::after {
                content: none;
            }
        }
    }

    &__title {
        color: $branco;
        font-family: $primaryFont;
        font-size: 37px;
        font-weight: 500;
        text-transform: uppercase;

        @media screen and (max-width: 992px) {
            text-align: center;
            padding: 10px;
            color: #000;
        }
    }

    &__texto {
        color: $branco;
        font-size: 17px;
        font-weight: 300;
        line-height: 22.92px;
        width: 85%;

        strong {
            color: $branco;
            font-weight: 500;
        }

        @media screen and (max-width: 992px) {
            width: 100%;
            text-align: center;
            padding: 10px 20px;
        }
    }

    .newsletter__form {
        width: 624px;
        display: block;
        margin-left: 39px;

        @media screen and (max-width: 992px) {
            width: 90%;
            margin-left: 0;

        }
    }

    &--name {
        float: left;
        width: 100%;
        margin: 5px 0;
        padding-left: 21px !important;
    }

    &--email {
        float: left;
        width: 460px;
        margin: 5px 0;
        padding-left: 21px !important;

        @media screen and (max-width: 992px) {
            width: 70% !important;
        }
    }

    &--button {
        float: left;
        border: none;
        width: 164px;
        margin: 5px 0;
        cursor: pointer;

        @media screen and (max-width: 992px) {
            width: 30% !important;
            position: initial;
            margin-top: 16px;
        }
    }
}

.logoBottom {

    padding: 40px 0;

    text-align: center;

    @media screen and (max-width: 768px) {
        max-width: 100%;
        padding: 30px 0 26px 0 !important;
    }

    span {

        a {

            svg {
                width: 280px;
                height: auto;
                padding: 0 40px;
            }
        }
    }
}

footer {

    background-color: #302e2b;
    position: relative;

    .footer-container {

        padding: 40px 20px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        border-bottom: 1px solid $branco;
        margin-bottom: 20px;

        @media screen and (max-width: 768px) {
            flex-direction: column;
        }

        .footer-atendimento {

            &__title {
                font-size: 15px;
                font-weight: 500;
                text-transform: uppercase;
                line-height: 27.08px;
                color: $branco;
                position: relative;
                margin-bottom: 20px;

                &::before {
                    content: '\e905';
                    font-family: 'icomoon';
                    font-size: 20px;
                    font-weight: 700;
                    color: $branco;
                    position: absolute;
                    cursor: pointer;
                    left: -45px;
                    top: 0;
                    transition: all .5s;
                }

                &.ativo {

                    &::before {
                        content: '\e905';
                        font-family: 'icomoon';
                        font-size: 20px;
                        font-weight: 700;
                        color: $branco;
                        position: absolute;
                        cursor: pointer;
                        left: -45px;
                        top: 0;
                        transform: rotate(180deg);
                        transition: all .5s;
                    }
                }
            }

            &__title.ativo::before {
                content: '\e905';
                font-family: 'icomoon';
                transform: rotate(180deg);
                font-size: 20px;
                font-weight: 700;
                color: $branco;
                position: absolute;
                cursor: pointer;
                left: -45px;
                top: 0;
            }

            @media screen and (max-width: 768px) {

                &__title {
                    border-bottom: 1px solid $branco;
                }

                &__title::before {
                    content: '';
                    position: absolute;
                    background-color: $branco;
                    width: 2px;
                    height: 10px;
                    top: 10px;
                    right: 5px;
                    left: initial;
                }

                &__title::after {
                    content: '';
                    position: absolute;
                    background-color: $branco;
                    width: 10px;
                    height: 2px;
                    top: 14px;
                    right: 1px;
                }

                &__title.ativo::before {
                    content: none;
                }
            }

            &__list-item {
                color: $branco;
                line-height: 33.33px;
                font-size: 14px;
                font-weight: 300;

                &:nth-child(4) {
                    margin-top: 20px;
                }
            }
        }

        .footer-links {

            &__title {
                font-size: 15px;
                font-weight: 500;
                text-transform: uppercase;
                line-height: 27.08px;
                color: $branco;
                margin-bottom: 20px;

                @media screen and (max-width: 768px) {
                    position: relative;
                    border-bottom: 1px solid $branco;
                }
            }

            &__link {
                display: flex;
                align-items: center;
                justify-content: flex-start;

                img {
                    margin-right: 5px;
                }

                @media screen and (max-width: 768px) {
                    justify-content: flex-start;
                }
            }

            @media screen and (max-width: 768px) {
                &__list {
                    margin-bottom: 15px;
                }

                &__title.ativo::before {
                    content: none;
                }

                &__title::before {
                    content: '';
                    position: absolute;
                    background-color: $branco;
                    width: 2px;
                    height: 10px;
                    top: 10px;
                    right: 5px;
                }

                &__title::after {
                    content: '';
                    position: absolute;
                    background-color: $branco;
                    width: 10px;
                    height: 2px;
                    top: 14px;
                    right: 1px;
                }
            }

            &__list-item {
                color: $branco;
                line-height: 33.33px;
                font-size: 14px;
                font-weight: 300;
            }
        }

        .footer-links:last-child {

            // img {
            //     width: 45px;
            //     height: auto;
            // } 

            text-align: center;

            @media screen and (max-width: 768px) {
                text-align: left;
            }
        }

        @media screen and (max-width: 768px) {

            .footer-links:last-child .footer-links__list:not(.hide) {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
            }
        }
    }

    .footer-copyright {

        padding-bottom: 20px;
        display: flex;
        flex-direction: row;

        @media screen and(max-width: 768px) {
            flex-direction: column;
        }

        &__text {
            color: $branco;
            font-size: 13px;
            line-height: 24px;
            font-weight: 300;
            opacity: 0.4;

            @media screen and(max-width: 768px) {
                font-size: 10px;
                padding: 10px;
                text-align: center;
            }
        }

        &__logos {
            display: flex;
            flex-direction: row;
            width: 40%;
            justify-content: space-around;

            @media screen and(max-width: 768px) {
                width: 100%;
            }
        }

        &__logo-eplus {
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            align-items: center;
            width: 50%;

            @media screen and(max-width: 768px) {
                width: 100%;
                justify-content: center;
            }
        }

        &__link {
            &:nth-child(1) {
                margin-right: 20px;
            }
        }

        &__logo-vtex {
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            align-items: center;
            width: 50%;

            @media screen and(max-width: 768px) {
                display: none;
            }
        }
    }
}

.quem-somos-departamento {
    .titulo-quemSomos::first-letter {
        text-transform: uppercase;
    }

    .container-center {
        p {
            text-align: center;
        }
    }
}

.footer-pop-up {
    display: flex !important;
    justify-content: center;
    position: fixed;
    /* background: #F6DED2; */
    background: #000;
    bottom: 0;
    width: 100%;
    height: 50px;
    z-index: 999;

    &.no {
        display: none !important;
    }

    & span {
        font-weight: 500;
        display: flex;
        align-items: center;
        margin-bottom: 5px;
        /* color: #4c3b41; */
        color: #fff;
        font-family: 'Montserrat';
        text-align: center;
        font-size: 13px;
        line-height: 100%;
        padding: 13px 0px 0px 0px;
    }

    & span button {
        text-decoration: underline;
        background: unset;
        border-radius: 8px;
        padding: 5px;
        border: 0;
        margin-left: 5px;
        font-weight: 700;
        color: #fff;
        transition: all 0.5s;
        cursor: pointer;
        margin-left: 20px;
    }

    & span button:hover {
        opacity: 0.8;
    }

    @media only screen and (max-width: 1024px) {
        & {
            height: 55px;
        }

        & span {
            flex-wrap: wrap;
            justify-content: center;
        }
    }


    @media only screen and (max-width: 767px) {
        & span {
            padding: 20px;
        }

        & {
            height: 100px;
        }
    }

    @media only screen and (max-width:450px) {
        & span {
            padding: 5px 20px;
        }

        & span a {
            margin: 0;
        }
    }
}

@keyframes error {
    0% {
        transform: translateX(0);
    }

    25% {
        transform: translateX(-1%);
    }

    50% {
        transform: translateX(0);
    }

    75% {
        transform: translateX(1%);
    }

    100% {
        transform: translateX(0);
    }
}