.miniCart {
  cursor: pointer;
  position: relative;
  z-index: 7;
  width: 35px;
  height: 31px;
  text-align: center;

  @media screen and (max-width: 992px) {
    height: unset;
    width: unset;
  }

  &__icon-cart {
    @media screen and (max-width: 992px) {
      padding: 50px 0 15px 0;
    }
  }

  &__icon-cart::before {
    content: "\e904";
    font-family: "icomoon";
    font-size: 1.8rem;
    font-weight: 400;
    cursor: pointer;
  }

  &__info-cart.ativo {
    display: block;
    right: 0;
    z-index: 3;

    @media screen and (max-width: 992px) {
      position: fixed;
      top: 0;
      right: 0;
    }
  }

  &__info-cart {
    position: absolute;
    top: -35px;
    right: -999px;
    width: 400px;
    background-color: $branco;
    transition: right 0.6s;

    @media screen and (max-width: 992px) {
      display: none;
      width: 300px;
    }
  }

  &__title {
    height: 50px;
    display: flex;
    align-items: center;

    & .miniCart__qtd-cart {
      color: #FFF;

      &::before {
        content: "("
      }

      &::after {
        content: ")"
      }
    }
  }

  &__close {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 15%;
    height: 100%;
    justify-content: center;
    padding: 10px;
    background-color: #383838;
    color: #fff;
    font-weight: 700;
    cursor: pointer;
  }

  &__title-text {
    text-transform: uppercase;
    color: #fff;
    padding: 10px;
    background-color: #878c8c;
    width: 85%;
    height: 100%;
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
  }

  &__qtd-cart {
    padding-left: 5px;
    color: $preto;
  }

  &__body {
    max-height: 310px;
    min-height: 310px;
    overflow-y: scroll;

    @media screen and (max-width: 1500px) {
        min-height: 275px;
    }
  }

  &__empty-text {
    padding: 20px;
    font-weight: bold;
  }

  &__product {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 15px;
    border-bottom: 1px solid #c1c1c1;
  }

  &__product-img {
    min-width: 70px;
    min-height: 60px;
    border: 1px solid #c1c1c1;
    text-align: center;
  }

  &__image {
    max-width: 100%;
    height: auto;
  }

  &__product-infos {
    padding: 0 10px;
  }

  &__product-name {
    font-size: 12px;
    font-weight: 600;
    overflow: hidden;
    margin-bottom: 3px;
    text-align: left;
    font-family: $thirdFont;
  }

  &__product-price {
    font-size: 15px;
    font-weight: bold;
    margin-bottom: 3px;
    text-align: left;
    font-family: $thirdFont;
  }

  &__product-qtd-wrapper::before {
    content: "Qtd:";
    font-size: 12px;
  }

  &__product-qtd-wrapper {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  &__product-qtd-btn-minus {
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid #c1c1c1;
    text-align: center;
    margin-left: 10px;
    cursor: pointer;
    line-height: 25px;
    font-size: 20px;
  }

  &__product-qtd {
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid #c1c1c1;
    text-align: center;
    line-height: 30px;
  }

  &__product-qtd-btn-plus {
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid #c1c1c1;
    text-align: center;
    cursor: pointer;
    line-height: 30px;
    font-size: 20px;
  }

  &__product-remove-btn {
    position: relative;

    &::before {
      content: "\e9ad";
      font-family: "icomoon";
      font-size: 0.8rem;
      font-weight: 600;
      cursor: pointer;
      color: #878c8c;
    }
  }

  &__footer {
    background-color: #f8f8f8;
    padding: 10px 0;
  }

  &__total-wrapper {
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px 30px;
    justify-content: space-between;
    border-bottom: 1px solid #c1c1c1;
  }

  &__buttons {
    background-color: #f8f8f8;
    padding: 10px 0;
    display: flex;
    flex-direction: column;
  }

  &__btn-checkout {
    order: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    color: $branco;
    background-color: #878c8c;
    height: 50px;
    width: 90%;
    margin: 10px auto 15px auto;
    cursor: pointer;
  }

  &__btn-continue {
    order: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    color: #8C9191;
    background-color: $branco;
    border: 1px solid #8C9191;
    height: 50px;
    width: 90%;
    margin: 0 auto 10px;
    cursor: pointer;
  }

  .eclipse {
    position: absolute;
    bottom: 0;
    right: 2px;
    width: 15px;
    height: 15px;
    border: 1px solid #191e25;
    background-color: #191e25;
    color: #fff;
    border-radius: 50%;
    text-align: center;
    font-size: 10px;
    font-weight: 700;
    padding: 1px;
    z-index: 2;
  }
}
