.prateleira {
    &__productImage-container{
        width: 100%;
        position: relative;
        &:hover{
            .prateleira__productImage{
                &:last-of-type{
                    opacity: 1;
                    transition: all 200ms ease-in;
                    z-index: 1;
                    min-height: 325px;
                }
            }
        }
    }
    &__productImage{
        &:first-of-type{
            img{
                width: 100%;
                height: auto;
            }
        }
        &:last-of-type{
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            opacity: 0;
            transition: all 200ms ease-in;
            z-index: -1;
            img{
                width: 100%;
                height: auto;
            }
        }
    }
}