$primary-color: #302e2b;
$secondary-color: $branco;

body.institucional {
	.sobre-maluli {
		display: none;
	}
}

div.institucional {
	display: flex;
	margin-bottom: 140px;

	.sidebar {
		margin: 0 40px 0 0;
	}

	.menuLat {
		&__title {
			text-transform: uppercase;
			font-size: calculadora-rem(24);
			margin: 0 0 6px;
			color: $primary-color;
			margin-bottom: 20px;
		}

		&__item {
			border: 1px solid #eee;
			margin: 0 0 8px;
		}

		&__link {
			display: block;
			width: 280px;
			height: 40px;
			line-height: 40px;
			font-size: calculadora-rem(14);
			padding: 0 0 0 8px;
			text-transform: uppercase;
			transition: border 0.3s;

			&:hover {
				background-color: $primary-color;
				color: $secondary-color;
				border-left: 5px solid $secondary-color;
				font-weight: 700;
			}
		}
	}

	.institucional-content {
		padding: 0 10px;

		h1 {
			text-transform: uppercase;
			font-size: calculadora-rem(24);
			margin: 0 0 20px;
			color: $primary-color;
			text-align: left;
		}

		p {
			// line-height: 1.5;
			margin: 0 0 8px;
			font-size: calculadora-rem(14);
		}

		ul {
			margin: 0 0 8px;
			padding: 0 15px;

			li {
				list-style: none;
				text-align: left;
			}
		}

		ol {
			margin: 0 0 8px;
			padding: 0 15px;

			li {
				list-style: decimal;
			}
		}

		li {
			font-size: calculadora-rem(14);
			list-style: none;
		}

		h2 {
			text-align: left;
		}

		h3 {
			margin: 20px 0 10px;
			text-transform: uppercase;
			text-align: left;
			// color: $cinza-claro-font;
		}
	}
}

body.institucional-esp,
body.institucional-ing {
	.sidebar {
		display: none;
	}
}

body {

	&.quem-somos #linkInstQuemSomos a,
	&.politica-de-privacidade #linkInstPrivacidade a,
	&.prazo-de-entrega #linkInstFrete a,
	&.formas-de-pagamento #linkInstPgto a,
	&.trocas-devolucoes #linkInstTrocas a,
	&.fale-conosco #linkFaleConosco a,
	&.faq #linkInstFaq a {
		background-color: $primary-color;
		color: $secondary-color;
		border-left: 5px solid $secondary-color;
		font-weight: 700;
	}
}

@media (max-width: 1024px) {
	div.institucional {
		flex-direction: column;
		margin: 30px auto 60px;

		.sidebar {
			margin: 0 0 20px;
		}

		.menuLat {
			&__title {
				border: 1px solid #000;
				padding: 5px;
				cursor: pointer;

				&::after {
					content: "\203A";
					display: block;
					float: right;
					transform: rotate(90deg);
					font-size: calculadora-rem(36);
					margin: -9px 0 0;
				}

				&.open::after {
					transform: rotate(-90deg);
					margin: -9px 7px 0 0;
				}
			}

			&__item {
				display: none;
			}

			&__link {
				width: 100%;
			}
		}

		.institucional-content {
			h1 {
				white-space: nowrap;
			}
		}
	}
}

@media screen and (max-width: 1240px) {
	div.institucional .institucional-content {
		padding: 0;
	}
}

#formFC {
	width: 100%;
	float: left;
}

#formFC .boxCampo {
	width: 100%;
	float: left;
	margin: 10px 0;
}

#formFC label,
#formFC input,
#formFC textarea {
	width: 100%;
	float: left;
}

#formFC input,
#formFC textarea {
	padding: 10px;
}

#formFC .enviarForm {
	width: 100%;
	float: left;
	background-color: $secondary-color;
	color: $primary-color;
	padding: 10px 0;
	text-align: center;
	margin-top: 20px;
	cursor: pointer;
	// font-family: 'Baloo Bhai';
	text-transform: uppercase;
	font-size: calculadora-rem(18);
}

#formFC #msgSucesso {
	width: 100%;
	padding: 10px;
	float: left;

	background: #f0f0f0;
	color: #000;
	text-align: center;
}