//fonte padro apenas para no dar erros na inicializao do Sass
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700;800;900&display=swap');

@font-face {
    font-family: 'icomoon';
    src: url(/arquivos/icomoon4.ttf.css);
    src: url(/arquivos/icomoon4.css#iefix) format('embedded-opentype'),
        url(/arquivos/icomoon4.woff.css) format('woff')
}

@font-face {
    font-family: 'Anjel Script Personal Use';
    src: url(/arquivos/AnjelScript.woff2.css) format('woff2'),
        url(/arquivos/AnjelScript.woff.css) format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Product Sans';
    src: url(/arquivos/ProductSans-Italic.woff2.css) format('woff2'),
        url(/arquivos/ProductSans-Italic.woff.css) format('woff');
    font-weight: normal;
    font-style: italic;
    font-display: swap;
}

@font-face {
    font-family: 'Product Sans';
    src: url(/arquivos/ProductSans-Regular.woff2.css) format('woff2'),
        url(/arquivos/ProductSans-Regular.woff.css) format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Product Sans';
    src: url(/arquivos/ProductSans-BoldItalic.woff2.css) format('woff2'),
        url(/arquivos/ProductSans-BoldItalic.woff.css) format('woff');
    font-weight: bold;
    font-style: italic;
    font-display: swap;
}

@font-face {
    font-family: 'Product Sans';
    src: url(/arquivos/ProductSans-Bold.woff2.css) format('woff2'),
