    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Myriad Pro';
    src: url(/arquivos/MyriadPro-Regular.woff2.css) format('woff2'),
        url(/arquivos/MyriadPro-Regular.woff.css) format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
