.slick-prev.slick-arrow {
    position: absolute;
    top: 30%;
    left: -65px;
    z-index: 1;
    border: 0;
    font-size: 0;
    width: 52px;
    height: 52px;
    background-color: rgba(234,234,234, .5);
    opacity: 0.8;
    border-radius: 50%;
    cursor: pointer;

    @media screen and (max-width: 991px) {
        // top: 235px;
        left:5px;
    }
}


.slick-prev.slick-arrow::before {
    content: '\e901';
    position: absolute;
    top: 10px;
    left: 13px;
    font-size: 30px;
    font-family: 'icomoon';
    font-weight: bold;
    color: #000;
}

.slick-next.slick-arrow {
    position: absolute;
    top: 30%;
    right: -55px;
    z-index: 1;
    border: 0;
    font-size: 0;
    width: 52px;
    height: 52px;
    background-color: rgba(234,234,234, .5);
    opacity: 0.8;
    border-radius: 50%;
    cursor: pointer;

    @media screen and (max-width: 991px) {
        // top: 235px;
        right: 5px;
    }
}


.slick-next.slick-arrow::before {
    content: '\e901';
    position: absolute;
    transform: rotate(180deg);
    top: 10px;
    right: 13px;
    font-size: 30px;
    font-family: 'icomoon';
    font-weight: bold;
    color: #000;
}

.slide-principal.desktop .slick-prev.slick-arrow {
    top: 50%;
    left: 270px;
    background-color: #1d1d1d;

    @media screen and (max-width: 1550px) {
        left: 40px;
        width: 42px;
        height: 42px;
    }
}

.slide-principal.desktop .slick-prev.slick-arrow::before {
    color: $branco;

    @media screen and (max-width: 1550px) {
        color: $branco;
        font-size: 20px;
    }
}

.slide-principal.desktop .slick-next.slick-arrow {
    top: 50%;
    right: 270px;
    background-color: #1d1d1d;

    @media screen and (max-width: 1550px) {
        right: 40px;
        width: 42px;
        height: 42px;
    }
}

.slide-principal.desktop .slick-next.slick-arrow::before {
    color: $branco;

    @media screen and (max-width: 1550px) {
        color: $branco;
        font-size: 20px;
    }
}

.slide-principal.mobile .slick-prev.slick-arrow {
    display: flex !important;
    justify-content: center;
    align-items: center;
    left: 10px;
    top: 45%;
    width: 33px;
    height: 34px;
    transform: translateY(-50%);
}

.slide-principal.mobile .slick-next.slick-arrow {
    right: 10px;
    top: 45%;
    width: 33px;
    height: 34px;
    transform: translateY(-50%) rotate(180deg);
}

.box-linhas .slick-prev.slick-arrow {
    display: block;
    left: 10px;
    top: 372px;
    background-color: $branco;
}

.box-linhas .slick-next.slick-arrow {
    display: block;
    right: 10px;
    top: 372px;
    background-color: $branco;
}

.slide-principal.mobile .slick-prev.slick-arrow::before {
    position: absolute;
    top: 50%;
    left: 45%;
    transform: translate(-50%, -50%);
    font-size: 20px;
}

.slide-principal.mobile .slick-next.slick-arrow::before {
    position: absolute;
    top: 50%;
    left: 45%;
    transform: translate(-50%, -50%);
    font-size: 20px;
    right: auto;
}

.slick-dots {
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: relative;
    top: -45px;
}

.slick-dots li button {
    font-size: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid #fff;  
    cursor: pointer;

    @media screen and (max-width: 991px) {
        width: 15px;
        height: 15px;
    }
}

.slick-dots li {
    margin: 0 5px;
}