header.desktop.fixo {
    position: fixed;
    width: 100%;
    z-index: 3;
    top: 0;
}

header.desktop {
    background-color: $branco;
    height: 95px;
    font-family: 'Montserrat', sans-serif;

    .container-center {

        .tipbar {
            width: 100%;
            display: flex;
            justify-content: flex-end;

            .canal-revendedor {
                display: flex;
                right: 0;
                top: 0;
                color: $branco;
                background-color: #8a8c8d;
                width: 205px;
                height: 30px;
                justify-content: center;
                align-items: center;
                text-transform: uppercase;
                font-weight: 400;
                font-size: 13px;
            }
        }

        .header-principal {
            display: flex;
            align-items: center;
            justify-content: space-between;
            height: 95px;

            .logo {
                @media screen and (max-width: 1500px) {
                    width: 250px;
                }

                a {
                    svg {
                        width: 280px;

                        @media screen and (max-width: 1500px) {
                            max-width: 100%;
                        }
                    }
                }
            }

            .menu-desktop {
                width: 72%;

                &__nav {
                    display: flex;
                    justify-content: space-between;
                    max-width: 100%;

                    &-sub {
                        margin-right: 20px;
                    }
                }

                &__nav-manual {
                    display: flex;
                    justify-content: space-between;
                    max-width: 100%;

                    &-sub {
                        margin-right: 20px;
                    }
                }
            }

            .busca-topo {

                position: relative;

                &__icon {}

                &__icon::before {
                    content: '\e903';
                    font-family: 'icomoon';
                    font-size: 2.5rem;
                    font-weight: 500;
                    cursor: pointer;
                }

                fieldset.busca.ativo {
                    opacity: 1;
                    z-index: 3;
                }

                fieldset.busca {

                    border: none;
                    position: absolute;
                    top: 50%;
                    right: 0;
                    width: 705px;
                    opacity: 0;
                    z-index: -1;
                    transform: translateY(-50%);
                    transition: opacity .5s ease-in-out;
                    padding: 0;

                    .fulltext-search-box {
                        width: 655px;
                        height: 45px;
                        float: left;
                        background-color: #f4f4f4;
                        border: none;
                        font-size: 0;
                        &:focus{
                            font-size: 15px;
                            padding: 0 0 0 15px;
                        }
                    }

                    .btn-buscar {
                        float: left;
                        font-size: 0;
                        width: 45px;
                        height: 45px;
                        border: 0;
                        background-color: #f4f4f4;
                        background-image: url(/arquivos/lupa.png);
                        background-repeat: no-repeat;
                        background-size: 60%;
                        background-position: center;
                        cursor: pointer;
                    }

                    legend,
                    label,
                    select {
                        display: none;
                    }
                }
            }

            .user-topo {
                position: relative;
                z-index: 1;

                &__icon::before {
                    content: '\e906';
                    font-family: 'icomoon';
                    font-size: 1.5rem;
                    font-weight: 400;
                    cursor: pointer;
                }

                &__hover {
                    position: absolute;
                    display: none;
                    width: 170px;
                    top: 100%;
                    left: -60px;
                    padding: 10px 20px;
                    line-height: 35px;
                    background-color: $branco;
                    font-size: 12px;

                    li {
                        border-bottom: 1px solid #C1d1d1;
                    }

                    li:last-child {
                        color: red;
                        font-weight: 500;
                        border: none;
                    }
                }

                &:hover {
                    .user-topo__hover {
                        display: block !important;
                        z-index: 1;
                    }
                }
            }

            .user-topo.ativo {
                color: $branco;
            }

            .logo.desktop {
                margin-right: 20px;
            }

            .flag-topo{
                margin: 0 5px 0 10px;

                &__uk{
                    width: 100%;
                    height: 20px;
                }

                &__esp{
                    width: 120%;
                    height: 20px;
                }
            }
        }

        @media screen and (max-width: 1400px) {
            width: 95%;
        }
    }

    .overlay {
        position: absolute;
        display: none;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.6);
    }

    .overlay.ativo {
        display: block;
        z-index: 0;
    }

    @media screen and (max-width: 992px) {}
}

header.mobile {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #302e2b;
    z-index: 6;

    .container {
        position: relative;
        display: flex;
        height: 80px;
        justify-content: space-between;
        align-items: center;
        padding: 10px 25px 0 25px;

        .menu-hamburguer {
            position: relative;
            display: block;
            width: 10%;
        }

        .menu-hamburguer::before {
            content: '\e902';
            font-family: 'icomoon';
            font-size: 2em;
            color: $branco;
        }

        .logo {

            width: 70%;
            text-align: center;

            svg {
                width: 155px;
                height: 31px;

                .cls-2 {
                    fill: #fff;
                }
            }
        }

        .menu-mobile {
            position: fixed;
            height: calc(100% - 238px);
            top: 0;
            left: -100%;
            width: 80%;
            background-color: $branco;
            text-indent: 10px;
            z-index: 3;

            &.ativo {
                left: 0;
            }

            .fechar-menu {
                position: absolute;
                width: 66px;
                height: 65px;
                top: 0;
                right: -66px;
                background-color: #fff;
                text-align: center;
                line-height: 65px;
                text-indent: 0;
                cursor: pointer;
                font-size: 20px;
                font-weight: 700;
                border-left: 1px solid rgba(0, 0, 0, 0.1);
            }

            &__user {
                background-color: #302e2b;
                padding: 10px 0 0;
                line-height: 35px;

                .user-icon,
                .meus-pedidos,
                .minha-conta,
                .sair-logoff {
                    display: flex;
                    align-items: center;
                    text-indent: 1px;
                    color: $branco;
                    text-transform: uppercase;
                    font-size: 12px;

                    a {
                        padding-top: 5px;
                    }
                }

                .meus-pedidos,
                .minha-conta,
                .sair-logoff {
                    padding-left: 30px;
                }

                .user-icon::before {
                    content: '\e906';
                    font-family: 'icomoon';
                    font-size: 1.5rem;
                    font-weight: 400;
                    padding: 0 5px;
                }

                .carrinho-icon {
                    display: flex;
                    align-items: center;
                    text-indent: 1px;
                    color: $branco;
                    text-transform: uppercase;
                    font-size: 12px;

                    a {
                        padding-top: 5px;
                    }
                }

                .carrinho-icon::before {
                    content: '\e908';
                    font-family: 'icomoon';
                    font-size: 1.5rem;
                    font-weight: 400;
                    color: $branco;
                    padding: 0 5px;
                }
            }

            .busca-mobile {

                position: relative;
                height: 65px;
                background-color: $branco;

                fieldset.busca {

                    border: 0;
                    padding: 10px;

                    legend,
                    label,
                    select {
                        display: none;

                    }

                    .fulltext-search-box {
                        position: absolute;
                        border-radius: 30px;
                        height: 40px;
                        border: 1px solid #919191;
                        left: 10px;
                        width: 90%;
                        z-index: 0;
                        color: #919191;
                        font-family: Montserrat;
                        font-size: 10px !important;
                        letter-spacing: -0.4px;
                    }

                    .btn-buscar {
                        position: absolute;
                        right: 25px;
                        width: 40px;
                        height: 40px;
                        border: 1px solid transparent;
                        border-radius: 50%;
                        z-index: 1;
                        font-size: 0;
                        background-image: url(/arquivos/lupa.png);
                        background-repeat: no-repeat;
                        background-size: 50%;
                        background-position: center;
                        cursor: pointer;
                        background-color: transparent;
                    }
                }
            }

            &__nav {
                background-color: $branco;
                max-height: 100%;
                height: 100%;
                overflow: auto;

                &--list {
                    padding-left: 10px;
                    z-index: 3;
                }

                &--list-nivel-1-item--no-child {
                    display: none;
                }

                &--list-nivel-1-item--ver-mais {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    font-size: 14px;
                    font-weight: bold;
                    text-transform: uppercase;
                    min-height: 50px;
                    background-color: #e8e8e8;

                    @media screen and (max-width: 991px) {
                        font-weight: normal;
                    }
                }

                &--list-nivel-1-title {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    font-size: 14px;
                    font-weight: bold;
                    text-transform: uppercase;
                    min-height: 50px;
                    border-top: 1px solid #f3f3f3;
                    // pointer-events: none;
                    padding: 16px 0;
                }

                &--list-container.ativo {
                    height: 100%;
                    opacity: 1;
                    overflow: initial;
                }

                &--list-container {
                    display: none;
                }

                &--list-container-no-child {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    font-size: 14px;
                    font-weight: bold;
                    text-transform: uppercase;
                    min-height: 50px;
                    background-color: #e8e8e8;

                    @media screen and (max-width: 991px) {
                        font-weight: normal;
                    }
                }

                &--list-nivel-1-title::before {
                    content: '';
                    position: absolute;
                    width: 10px;
                    height: 2px;
                    background-color: #000;
                    right: 10px;
                    top: 24px;
                }

                &--list-nivel-1-title::after {
                    content: '';
                    position: absolute;
                    width: 2px;
                    height: 10px;
                    background-color: #000;
                    right: 14px;
                    top: 20px;
                }


                &--list-nivel-1-title.ativo::after {
                    display: none;
                }
            }

            .canal-revendedor {
                background-color: #8a8c8d;

                li {
                    min-height: 50px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    font-size: 14px;
                    text-transform: uppercase;
                    color: #fff;
                    font-weight: bold;
                    position: relative;
                }

                li::before {
                    content: '';
                    position: absolute;
                    width: 10px;
                    height: 2px;
                    background-color: $branco;
                    right: 10px;
                }

                li::after {
                    content: '';
                    position: absolute;
                    width: 2px;
                    height: 10px;
                    background-color: $branco;
                    right: 14px;
                }
            }
        }

        .busca-topo {
            width: 10%;
            margin-right: 8px;
            padding-top: 4px;

            &__icon {
                display: flex;
            }

            &__icon::before {
                content: '\e903';
                font-family: 'icomoon';
                font-size: 2.5rem;
                font-weight: 400;
                color: $branco;
            }
        }

        .miniCart {
            width: 10%;
            position: relative;
            z-index: 1;

            &__icon-cart::before {
                content: '\e908';
                font-family: 'icomoon';
                font-size: 1.7rem;
                font-weight: 400;
                color: $branco;
            }

            .eclipse {
                position: absolute;
                bottom: 0px;
                left: 17px;
                width: 17px;
                height: 17px;
                border: 1px solid #fff;
                background-color: #fff;
                color: #000;
                border-radius: 50%;
                text-align: center;
                font-size: 12px;
                font-weight: 700;
                padding: 1px;
                line-height: 13px;
            }
        }

        @media screen and (min-width: 991px) {
            display: none;
        }
    }
}

