/* Reset css */
* {
  margin: 0 auto;
  padding: 0 auto;
  color: #000;
  outline-color: transparent !important;
  scroll-behavior: smooth;
}

*:focus {
  outline-color: transparent !important;
}

*, *:before, *:after {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  line-height: inherit;
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  margin: 0;
  padding: 0;
  line-height: initial;
}

p {
  padding: 0;
  margin: 0;
}

a:focus, a:active,
button:focus,
button:active,
.btn:focus,
.btn:active,
.button:focus,
.button:active {
  outline: none;
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
  color: inherit;
}

select,
textarea,
input[type=text],
input[type=password],
input[type=datetime],
input[type=datetime-local],
input[type=date],
input[type=month],
input[type=time],
input[type=week],
input[type=number],
input[type=email],
input[type=url],
input[type=search],
input[type=tel],
input[type=color],
.uneditable-input {
  display: inline-block;
  padding: inherit;
  margin-bottom: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  vertical-align: middle;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  height: 40px;
}

input[type=checkbox],
input[type=radio],
select[type=checkbox],
select[type=radio],
textarea[type=checkbox],
textarea[type=radio] {
  padding: 0;
}

