/* Inicio load */
body > #ajaxBusy.loading {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 100px;
  height: 100px;
  margin: auto;
  display: block !important;
  flex-flow: row nowrap;
  justify-content: space-around;
  z-index: 999999;
  text-indent: -99999px;
  text-align: center;
  background-color: transparent;
}

body > #ajaxBusy.loading[style="display: none;"] {
  display: none !important;
}

body > #ajaxBusy.loading p {
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  border-style: solid;
  animation: loader 500ms linear infinite;
  border-width: 3px;
  border-color: #333 transparent;
  z-index: 9999;
  margin: -25px 0 0 -25px;
  top: 50%;
  left: 50%;
}

@keyframes loader {
  to {
    transform: rotate(360deg);
  }
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-ms-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-o-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
