body.outOfStock .produto-direita__buy-button--fixed.show,
body.outOfStock .produto-direita__quantidade--fixed.show,
body.outOfStock .calculate-freight {
  display: none !important;
}

.modal-installments {
  display: none;
  width: 15%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  @media screen and (max-width: 992px) {
    width: 80%;
    max-height: 100%;
    overflow-Y: auto;
  }

  &.ativo {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #fff;
    z-index: 5;
    padding: 10px;
  }

  &__close {
    cursor: pointer;
    position: absolute;
    top: -15px;
    right: -15px;
    background-color: #8a8c8d;
    color: #FFF;
    border-radius: 50%;
    padding: 5px 10px;
    font-size: 24px;
    font-family: $fourFont;
    font-weight: 700;

    @media screen and (max-width: 992px) {
      top: 0;
      right: 0;
      font-size: 18px;
    }
  }

  &__type {
    width: 100%;
    margin-bottom: 10px;

    &~& {
      display: none;
    }

    @media screen and (max-width: 992px) {
      width: 100%;
    }

  }

  &__type-name {
    background-color: #8a8c8d;
    color: #FFF;
    text-align: center;
    padding: 5px;
    font-family: $fourFont;

    @media screen and (max-width: 992px) {
      font-size: 12px;
    }

  }

  &__installment {
    padding: 2px;
    font-family: $thirdFont;
    font-size: 14px;
    text-align: center;

    @media screen and (max-width: 992px) {
      font-size: 10px;
    }

    &:nth-child(even) {
      background-color: #f8f8f8;
    }
  }
}

.produto {
  main {
    border-top: 1px solid #f1f1f1;
  }

  .instafeed {
    display: none;
  }

}

.produto .barrinha {
  margin-top: 60px;

  @media screen and (max-width: 992px) {
    margin-top: 20px;
  }
}

.produto-geral {
  clear: both;
  display: flex;
  flex-direction: row;
  padding-top: 45px;
  padding-bottom: 30px;

  @media screen and (max-width: 992px) {
    flex-wrap: wrap;
  }

  .produto-esquerda {
    width: 70%;

    @media screen and (max-width: 992px) {
      width: 100%;
    }

    &__img {
      max-width: 100%;
      display: flex;

      @media screen and (max-width: 992px) {
        width: 100%;
        flex-direction: column;
        flex-wrap: wrap;
      }
    }

    &__thumbs {
      width: 117px;
      top: -30px;
      padding: 30px 0;
      height: 540px;

      .slick-list {
        max-height: 100%;
      }

      @media screen and (max-width: 992px) {
        display: none;
        width: 100%;
        order: 2;
      }

      .slick-prev.slick-arrow {
        top: 0;
        left: 50%;
        width: 30px;
        height: 20px;
        transform: translateX(-50%);
        background-color: transparent;
      }

      .slick-prev.slick-arrow::before {
        content: "\e907";
        position: absolute;
        top: 50%;
        left: 50%;
        font-size: 18px;
        font-family: icomoon;
        font-weight: 700;
        color: #000;
        transform: translate(-50%, -50%) rotate(180deg);
      }

      .slick-next.slick-arrow::before {
        content: "\e907";
        position: absolute;
        top: 50%;
        left: 50%;
        right: auto;
        font-size: 18px;
        font-family: icomoon;
        font-weight: 700;
        color: #000;
        transform: translate(-50%, -50%);
      }

      .slick-next.slick-arrow {
        top: unset;
        bottom: 0;
        left: 50%;
        width: 30px;
        height: 20px;
        transform: translateX(-50%);
        background-color: transparent;
      }

      &-img {
        width: 117px;
        height: 117px;

        @media screen and (max-width: 992px) {
          width: 100%;
          order: 1;
        }
      }
    }

    &__thumbs-item {
      display: none;
    }

    &__container {
      width: 85%;
      position: relative;

      @media screen and (max-width: 992px) {
        width: 100%;
      }

      &-img {
        max-width: 98%;
        margin: 5px auto;

        @media screen and (max-width: 992px) {
          max-width: 100%;
        }
      }



      &--selos {
        width: 550px;
        height: 550px;
        border: 1px solid #eeeeee;
        position: absolute;
        top: -15px;
        left: 130px;
        z-index: 2;

        @media screen and (max-width: 992px) {
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
        }

        @media only screen and (min-width: 1024px) and (max-width: 1399px) {
          width: 465px;
          height: 465px;
          border: 1px solid #eee;
          position: absolute;
          top: -15px;
          left: 130px;
        }

        @media screen and (min-width: 1400) and (max-width: 1500px) {
          width: 550px;
          height: 550px;
          left: 130px;
        }
      }

      .black-friday {
        position: absolute;
        right: 5px;
        top: 65px;
        width: 48px;
        height: 48px;
        background-color: #e5e5e5;
        color: #302e2b;
        font-family: $thirdFont;
        font-size: 10px;
        font-weight: 400;
        line-height: 14.58px;
        text-transform: uppercase;
        border-radius: 50%;
        display: flex;
        align-items: center;
        text-align: center;
        z-index: 1;
      }

      .frete-gratis {
        position: absolute;
        right: 5px;
        top: 10px;
        width: 48px;
        height: 48px;
        background-color: #302e2b;
        color: $branco;
        font-family: $thirdFont;
        font-size: 10px;
        font-weight: 400;
        line-height: 14.58px;
        text-transform: uppercase;
        border-radius: 50%;
        display: flex;
        align-items: center;
        text-align: center;
        z-index: 1;
      }

      .priceLabel {
        position: absolute;
        top: 10px;
        left: 10px;
        z-index: 1;
        background-color: $branco;
        font-size: 13px;
      }

      &--image {
        width: 550px;
        height: 550px;
        position: absolute;
        top: -15px;
        left: 130px;

        @media only screen and (min-width: 1024px) and (max-width: 1399px) {
          width: 465px;
          height: 465px;
          position: absolute;
          top: -15px;
          left: 130px;
        }

        @media screen and (max-width: 992px) {
          position: static;
          width: 100%;
          height: 100%;
        }

        .slick-prev.slick-arrow {
          left: -82px;
          top: 50%;
          z-index: 2;
          background-color: rgba(29, 29, 29, 0.05);

          @media screen and (max-width: 992px) {
            background-color: hsla(0, 0%, 91.8%, .5);
          }

          &::before {
            opacity: 0.15;

            @media screen and (max-width: 992px) {
              opacity: 1;
            }

          }


          &.clicked {
            background-color: hsla(0, 0%, 91.8%, .5);

            &::before {
              opacity: 1;
            }
          }

          @media screen and (max-width: 992px) {
            left: 10px;
            top: 45%;
          }
        }

        .slick-next.slick-arrow {
          right: -82px;
          top: 50%;
          z-index: 2;

          @media screen and (max-width: 992px) {
            right: 10px;
            top: 45%;
          }
        }
      }
    }

    &__container-item {
      display: none;
    }
  }

  .produto-direita {
    width: 30%;

    @media screen and (max-width: 992px) {
      width: 100%;
      padding: 10px;
    }

    &__selos-wrapper {
      display: flex;
      padding-bottom: 10px;

      @media screen and (max-width: 992px) {
        margin: 20px 0 0;
        justify-content: center;
      }
    }

    &__selos {
      visibility: hidden
    }

    &__selos--cinza {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      background-color: #3e3e3e;
      width: 165px;
      height: 35px;
      color: $branco;
      text-transform: uppercase;
      padding: 0;
      font-size: 15px;
      font-weight: 400;
      line-height: 13.13px;

      @media screen and (max-width: 992px) {
        width: 50%;
      }
    }

    &__selos--azul {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      background-color: #021c3f;
      width: 165px;
      color: $branco;
      text-transform: uppercase;
      padding: 0;
      font-size: 15px;
      font-weight: 400;
      line-height: 13.13px;

      @media screen and (max-width: 992px) {
        width: 50%;
      }
    }

    &__colecao {
      padding-bottom: 10px;
    }

    &__colecao--text {
      font-family: $fourFont;
      color: #949494;
      font-size: 15px;
      font-weight: 400;
      line-height: 24px;
      text-transform: capitalize;
    }

    &__reference {
      font-weight: bold;
      color: #545454;

      span {
        color: #545454;
      }
    }

    &__title {
      .productName {
        color: #545454;
        font-family: $thirdFont;
        font-size: 21px;
        font-weight: 700;
        line-height: 24px;
        text-transform: uppercase;
        margin-bottom: 15px;

        @media screen and (max-width: 1500px) {
          font-size: 18px;
        }
      }
    }

    &__price {
      position: relative;

      .valor-de.price-list-price {
        display: flex !important;
        float: left;
        align-items: center;
        height: 25px;
        padding-right: 10px;
        font-size: 12px;
        text-decoration: line-through;
        color: #b1b1b1;
        font-style: normal;

        @media screen and (max-width: 1500px) {
          font-size: 10px;
        }

        strong {
          color: #b1b1b1;
          font-size: 14px;

          @media screen and (max-width: 1500px) {
            font-size: 12px;
          }
        }
      }

      .valor-por.price-best-price {
        font-size: 0;
        margin-bottom: 5px;

        .skuBestPrice {
          color: #000000;
          font-family: $thirdFont;
          font-size: 25px;
          font-weight: 700;
          line-height: 24px;
          text-transform: uppercase;
          font-style: normal;

          @media screen and (max-width: 1500px) {
            font-size: 23px;
          }
        }
      }

      .valor-dividido.price-installments {
        font-style: normal;
        margin-bottom: 10px;

        span {
          color: #110e0b;
          font-family: $thirdFont;
          font-weight: 400;
          font-size: 14px;

          .skuBestInstallmentNumber,
          .x {
            color: #000000;
            font-family: $thirdFont;
            font-weight: 700;
          }

          .skuBestInstallmentValue {
            color: #000000;
            font-family: $thirdFont;
            font-weight: 700;
          }
        }
      }

      .preco-a-vista.price-cash {
        font-size: 0px;

        strong {
          display: none;
          color: #484848;
          font-family: $thirdFont;
          font-size: 17px;
          line-height: 24px;
          margin-bottom: 20px;

          &::before {
            content: " vista no boleto ";
            color: #484848;
            font-family: $thirdFont;
            font-size: 17px;
            font-weight: 400;
            line-height: 24px;
            margin-bottom: 20px;


            @media screen and (max-width: 1500px) {
              font-size: 14px;
            }
          }

          @media screen and (max-width: 1500px) {
            font-size: 14px;
          }
        }
      }

      .economia-de {
        display: none;
      }
    }

    &__allInstallments {
      cursor: pointer;
      position: absolute;
      right: 15%;
      bottom: 45%;
      display: flex;
      align-items: center;
      opacity: 0.7;
      color: #484848;
      font-family: "Product Sans";
      font-size: 10px;
      font-weight: 700;
      text-decoration: underline;

      @media screen and (max-width: 992px) {
        position: static;
        margin: 10px 0;
      }

      &::after {
        content: "";
        width: 0;
        height: 0;
        border-top: 5px solid transparent;
        border-bottom: 5px solid transparent;
        border-left: 5px solid #484848;
        margin-left: 3px;
      }
    }

    &__similares {
      min-height: 90px;

      .portobrasil-prateleira {
        display: flex;
        margin: 20px 0;
      }

      h2 {
        display: none;
      }

      h3 {
        color: #303030;
        font-family: $thirdFont;
        font-size: 14px;
        font-weight: 600;
      }

      ul {
        display: flex;
        flex-wrap: wrap;

        li {
          margin-bottom: 10px;
        }
      }


      &-item {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 10px 0 0;
        padding: 5px;
        border: 1px solid #dfdfdf;

        img {
          width: 50px;
          height: 50px;
        }
      }

      &-item:hover {
        border: 1px solid #272727;
        border-radius: 5px;
      }
    }

    &__box-qtd-comprar {
      display: flex;
      flex-direction: row;
      padding: 25px 0;
      border-top: 1px solid rgba(83, 83, 83, .3);
      border-bottom: 1px solid rgba(83, 83, 83, .3);

      @media screen and (max-width: 992px) {
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
      }
    }

    &__quantidade {
      display: flex;
      flex-direction: row;

      @media screen and (max-width: 992px) {
        width: 100%;
        justify-content: center;
      }
    }

    &__quantidade--fixed.show {
      display: flex !important;
      position: fixed;
      width: 30%;
      z-index: 9;
      left: 0;
      bottom: 0;

      .produto-direita__menos {
        width: 33%;
      }

      .produto-direita__itens {
        width: 33%;
      }

      .produto-direita__mais {
        width: 33%;
      }
    }


    &__menos {
      width: 32px;
      height: 52px;
      border: 1px solid #a2a2a2;
      background-color: #a2a2a2;
      color: $branco;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: bold;
      cursor: pointer;
    }

    &__itens {
      width: 40px;
      height: 52px;
      border: 1px solid #d7d7d7;
      background-color: $branco;
      text-align: center;
      font-weight: bold;
    }

    &__mais {
      width: 32px;
      height: 52px;
      border: 1px solid #a2a2a2;
      background-color: #a2a2a2;
      color: $branco;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: bold;
      cursor: pointer;
    }

    .buy-button-ref {
      text-align: center;
      height: 100%;
      width: 100%;
      line-height: 52px;
      position: relative;
      color: #ffffff;
      font-family: "Product Sans";
      font-size: 17px;
      font-weight: 700;
      text-transform: uppercase;
    }

    &__buy-button--fixed.show {
      display: block !important;
      position: fixed;
      width: 70%;
      z-index: 9;
      right: 0;
      bottom: 0;

      a.buy-button-ref {
        width: 100% !important;
        margin-left: 0px !important;
      }
    }

    &__buy-button {

      cursor: pointer;
      position: relative;


      a.buy-button-ref {
        width: 212px;
        height: 52px;
        background-color: #4e8809;
        position: relative;
        margin-left: 30px;

        @media screen and (max-width: 992px) {
          margin-left: 0px;
        }

        &::after {
          content: "\e909  COMPRA 100% SEGURA";
          position: absolute;
          width: 100%;
          bottom: -21px;
          left: 50%;
          transform: translateX(-50%);
          color: #6a6a6a;
          font-family: 'icomoon', $thirdFont;
          font-size: 13px;
          font-weight: 400;
          text-transform: uppercase;
          line-height: 1rem;
        }
      }

      @media screen and (max-width: 992px) {
        margin-top: 25px;
      }
    }
  }

  .notifyme.sku-notifyme {
    display: flex;
    flex-direction: column;

    .notifymetitle {
      font-family: 'Product Sans';
      text-transform: uppercase;
      margin-bottom: 5px;
    }

    form>fieldset {
      display: flex;
      flex-direction: column;

      input {
        border: 1px solid #dcdcdc !important;
      }
    }
  }

  .notifyme-title-div {
    width: 100%;
  }

  .sku-notifyme-form.notifyme-form {

    border: 0;
    display: flex;

    p {
      display: none;
    }

    .sku-notifyme-client-name.notifyme-client-name,
    .sku-notifyme-client-email.notifyme-client-email {
      height: 35px;
      margin-bottom: 5px;
    }

    .btn-ok.sku-notifyme-button-ok.notifyme-button-ok {
      text-transform: uppercase;
      border: 0;
      background-color: #302e2b;
      color: $branco;
      width: 100%;
      height: 35px;
    }
  }

  .kitlook {
    @media screen and (max-width: 992px) {
      margin-top: 0px;
    }

    &__header {
      background-color: #312d21;
      width: 100%;
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: center;

      &-title {
        color: $branco;
        font-family: $thirdFont;
        font-weight: 400;
        font-size: 21px;
        line-height: 28px;

        @media screen and (max-width: 992px) {
          font-size: 13px;
        }

        strong {
          font-weight: bold;
          color: $branco;
          font-size: 21px;
          font-family: $thirdFont;
          text-transform: uppercase;

          @media screen and (max-width: 992px) {
            font-size: 13px;
          }
        }
      }
    }

    &__list {
      max-height: 450px;
      overflow-x: hidden;
      overflow-y: scroll;

      @media screen and (max-width: 992px) {
        max-width: 100%;
      }

      &::-webkit-scrollbar {
        width: 7px;
        background-color: #cfcfcf;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #3b3b3b;
      }
    }

    &__item {
      display: flex;
      flex-direction: row;
      max-height: 150px;
      border-bottom: 1px solid #eeeeee;

      &:first-child {
        .kitlook__box-check {
          display: none;
        }
      }

      @media screen and (max-width: 992px) {
        min-height: 110px;
      }

      .thumbs {
        display: none;
      }
    }

    &__image {
      width: 125px;
      height: auto;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;

      @media screen and (max-width: 992px) {
        width: 70px;
      }

      @media screen and (max-width: 1500px) {
        width: 95px;
      }

      &::before {
        content: "";
        position: absolute;
        border: 1px solid #eeeeee;
        width: 100px;
        height: 95px;
        background-color: transparent;
        top: 15px;
        left: 13px;
        z-index: 2;

        @media screen and (max-width: 992px) {
          display: none;
          width: 50px;
          height: 45px;
          left: 12px;
          top: 30px;
        }

        @media screen and (max-width: 1500px) {
          left: 0;
        }
      }

      img {
        width: 125px;
        height: auto;
        padding: 10px;

        @media screen and (max-width: 992px) {
          width: 50px;
          padding: 0;
          border: 1px solid #eeeeee;
        }
      }
    }

    &__infos {
      display: flex;
      flex-direction: column;
      justify-content: center;
      min-width: 180px;
      max-width: 180px;
      padding: 10px;

      @media screen and (max-width: 992px) {
        max-width: 150px;
        min-width: 150px;
      }
    }

    &__product-name {
      font-family: $thirdFont;
      font-size: 14px;
      font-weight: 700;
      line-height: 20px;
      text-transform: uppercase;
      min-height: 40px;
      max-height: 40px;
      overflow: hidden;

      @media screen and (max-width: 992px) {
        font-size: 10px;
      }

      .productName {
        color: #545454;
        font-family: $thirdFont;
      }
    }

    &__product-prices {

      .valor-de {
        display: flex;
        float: left;
        align-items: center;
        height: 25px;
        padding-right: 10px;
        font-size: 1px;
        text-decoration: line-through;
        color: #b1b1b1;
        font-style: normal;

        @media screen and (max-width: 1500px) {
          font-size: 10px;
        }

        strong {
          color: #b1b1b1;
          font-size: 10px;

          @media screen and (max-width: 1500px) {
            font-size: 12px;
          }
        }
      }

      .valor-por {
        font-size: 0;

        .skuBestPrice {
          color: #000000;
          font-family: $thirdFont;
          font-size: 17px;
          font-weight: 700;
          line-height: 24px;
          text-transform: uppercase;
          font-style: normal;

          @media screen and (max-width: 992px) {
            font-size: 14px;
          }
        }
      }

      .valor-dividido {
        font-size: 14px;
        font-style: normal;
        margin-bottom: 15px;
        float: left;

        span {
          color: #110e0b;
          font-family: $thirdFont;
          font-weight: 400;
          font-size: 14px;

          @media screen and (max-width: 992px) {
            font-size: 10px;
          }

          .skuBestInstallmentNumber,
          .x {
            color: #000000;
            font-family: $thirdFont;
            font-weight: 700;
          }

          .skuBestInstallmentValue {
            color: #000000;
            font-family: $thirdFont;
            font-weight: 700;
          }
        }

        @media screen and (max-width: 992px) {
          font-size: 8px;
        }
      }

      .economia-de {
        display: none;
      }
    }

    &__box-check {
      display: flex;
      align-items: center;
    }

    &__product-label {
      order: 2;
      color: #040606;
      font-family: $thirdFont;
      font-size: 10px;
      font-weight: 400;
      text-transform: uppercase;
      text-indent: 5px;
      position: relative;
      cursor: pointer;
      padding-left: 15px;

      &::before {
        content: "";
        position: absolute;
        width: 15px;
        height: 15px;
        left: 0;
        bottom: 0;
        border: solid 1px;
        vertical-align: bottom;
      }

      &.selected::after {
        content: "";
        position: absolute;
        left: 5px;
        bottom: 3px;
        width: 6px;
        height: 12px;
        border-right: solid 2px #000;
        border-bottom: solid 2px #000;
        transform: rotate(30deg);
      }
    }

    span.kitlook__product-label {
      &::before {
        display: none;
      }
    }

    &__product-check {
      order: 1;
      position: absolute;
      z-index: -1;
      opacity: 0;
    }

    &__footer {
      display: flex;
    }

    &__totais {
      width: 50%;
      height: 50px;
      background-color: #312d21;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-left: 20px;

      @media screen and (max-width: 992px) {
        padding-left: 10px;
      }
    }

    &__quantity-text {
      font-family: $thirdFont;
      font-size: 14px;
      font-weight: 400;
      line-height: 23px;
      color: $branco;
    }

    &__quantity-total {
      font-family: $thirdFont;
      font-size: 14px;
      font-weight: 400;
      line-height: 23px;
      color: $branco;
    }

    &__price-text {
      font-family: $thirdFont;
      font-size: 14px;
      font-weight: 400;
      line-height: 23px;
      color: $branco;
    }

    &__price-total {
      font-family: $thirdFont;
      font-size: 14px;
      font-weight: bold;
      line-height: 23px;
      color: $branco;
    }

    &__buy-button {
      width: 50%;
      height: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    &__comprar {
      width: 100%;
      height: 100%;
      border: none;
      background-color: #4e8809;
      text-transform: uppercase;
      color: $branco;
      font-family: $thirdFont;
      font-size: 13px;
      cursor: pointer;

      @media screen and (max-width: 992px) {
        font-size: 12px;
      }
    }

    &__text-comprar {
      color: $branco;
      font-family: $thirdFont;
      font-weight: bold;
    }
  }

  .calculate-freight {
    position: relative;
    margin-top: 20px;

    @media screen and (max-width: 992px) {
      text-align: center;
    }

    &__title {
      display: block;
      text-transform: uppercase;
      font-size: 14px;
      font-weight: 600;
      font-family: $thirdFont;
      color: $preto;
      margin-bottom: 20px;
    }

    &__form {
      display: flex;
      justify-content: flex-start;

      @media screen and (max-width: 992px) {
        justify-content: center;
      }
    }

    &__zip-code {
      width: 228px;
      height: 45px;
      border: 1px solid #dcdcdc;
      margin-right: 5px;

      @media screen and (max-width: 1500px) {
        width: 150px;
      }
    }

    &__button {
      display: block;
      width: 86px;
      height: 45px;
      text-align: center;
      line-height: 45px;
      background-color: #656565;
      color: $branco;
      font-size: 17px;
      font-weight: bold;
      text-transform: uppercase;
      font-family: $thirdFont;
      letter-spacing: 1.67px;
      border: none;
      cursor: pointer;

      @media screen and (max-width: 992px) {
        margin: 0;
      }
    }

    &__dont-know {
      display: block;
      position: absolute;
      top: 60%;
      right: -15px;
      font-style: italic;
      text-decoration: underline;
      font-family: $thirdFont;
      font-size: 10px;

      @media screen and (max-width: 992px) {
        top: 100%;
        right: 65px;
        margin-top: 10px;
      }
    }

    &__table {
      display: none;
      position: absolute;
      top: 100%;
      left: 0;
      margin-top: 20px;

      @media screen and (max-width: 992px) {
        position: relative;
      }
    }

    &__table.ativo {
      display: block;
      width: 100%;
    }

    &__table-body {
      display: block;
      max-width: 100%;
    }

    &__table-first-row {
      display: flex;
      width: 100%;
      background-color: #8a8c8d;
      height: 30px;
      padding: 0px 5px;

    }

    &__table-head:first-child {
      width: 60%;
    }

    &__table-head {
      width: 20%;
      font-size: 12px;
      text-align: left;
      color: #FFF;
      line-height: 30px;
    }

    &__table-row {
      display: flex;
      width: 100%;
      padding: 0px 5px;
      height: 25px;

      &:nth-child(even) {
        background-color: #f8f8f8;
      }
    }

    &__table-td:first-child {
      width: 60%;
    }

    &__table-td {
      width: 20%;
      text-align: left;
      font-size: 12px;
      line-height: 25px;
    }
  }
}

.detalhes-prod {
  margin: 60px auto;
  position: relative;

  // &.hasChildren {
  //   background-color: #f8f8f8;
  // }

  &__title:first-child {
    top: -45px;
  }

  &__title.mais:first-child {
    top: 0px;
  }

  &__title {
    width: 485px;
    height: 45px;
    background-color: #8a8c8d;
    position: relative;
    color: $branco;
    display: flex;
    align-items: center;
    font-size: 16px;
    font-family: $thirdFont;
    text-transform: uppercase;
    text-indent: 10px;
    cursor: pointer;
    font-weight: 600;

    @media screen and (max-width: 992px) {
      width: 100%;
      font-size: 17px;
    }

    &::before {
      content: none;
    }

    &::after {
      content: '';
      position: absolute;
      top: 20px;
      right: 5px;
      width: 15px;
      height: 2px;
      background-color: #fff;
    }
  }

  &__title.mais {
    &::before {
      content: '';
      position: absolute;
      top: 20px;
      right: 5px;
      width: 15px;
      height: 2px;
      background-color: #fff;
      transform: rotate(90deg);
    }
  }

  &__details {
    display: none;
    padding: 20px;

    @media screen and (max-width: 992px) {
      padding: 20px;
    }
  }

  &__descriptions,
  &__uso-e-cuidado,
  &__info-complementares {
    padding-bottom: 30px;
    text-align: justify;
    overflow-wrap: break-word;
    word-wrap: break-word;
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
    width: 100%;
    font-size: 14px;

    td {
      text-align: justify;
      overflow-wrap: break-word;
      word-wrap: break-word;
      -webkit-hyphens: auto;
      -ms-hyphens: auto;
      hyphens: auto;
    }
  }

  &__descriptions {
    margin-top: -20px;
  }

  &__descriptions {
    padding-left: 20px;
    padding-right: 20px;
    font-size: 14px;
  }

  &__especificacoes {
    background-color: $branco;
    padding: 20px 0 0 0;
    position: relative;
    font-size: 14px;

    @media screen and (max-width: 992px) {
      padding: 20px
    }

    &::after {
      content: "";
      position: absolute;
      overflow: hidden;
      top: 0;
      right: -100%;
      width: 100%;
      height: 100%;
      background-color: $branco;
      z-index: 2;

      @media screen and (max-width: 992px) {
        content: none;
      }
    }

    br {
      display: none;
    }

    table {
      width: 485px;

      @media screen and (max-width: 992px) {
        width: 100%;
      }

      tr {
        height: 40px;
        background-color: #f8f8f8;

        td {
          width: 50%;
          padding: 10px;
          color: #3b3b3b;

          @media screen and (max-width: 992px) {
            width: auto;
            padding: 0 !important;
          }
        }

        td:nth-child(1) {
          text-indent: 0px;
        }

        td:nth-child(2) {
          font-weight: 500;
        }
      }

      tr:nth-child(odd) {
        background-color: $branco;
      }
    }
  }

  &__uso-e-cuidado {
    padding-top: 20px;

    table {
      tr {
        height: 40px;

        td:nth-child(1) {
          width: 20%;
          text-align: center;
        }

        td {
          padding: 20px;
        }
      }
    }
  }

  &__info-complementares {
    padding: 20px;
    overflow-wrap: break-word;
    word-wrap: break-word;
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
  }

  &__specifications {
    display: none;
  }
}

.compreJunto {
  padding: 50px 0;

  &__titulo {
    color: #8a8c8d;
    font-size: 25px;
    font-family: Product Sans;
    font-weight: 500;
    line-height: 24px;
    text-transform: uppercase;
    position: relative;
    margin-bottom: 50px;
    padding-left: 65px;

    @media screen and (max-width: 992px) {
      padding-left: 0px;
    }

    @media screen and (max-width: 375px) {
      width: 90%;
      font-size: 19px;
      line-height: 22.92px;
    }

    @media screen and (min-width: 420px) {
      width: 80%;
      font-size: 19px;
      line-height: 22.92px;
    }

    &::before {
      content: "";
      background-image: url(/arquivos/logo-sessao.png);
      position: absolute;
      width: 47px;
      height: 47px;
      top: -15px;
      left: 0;

      @media screen and (max-width: 992px) {
        display: none;
      }

    }

    &::after {
      content: "";
      position: absolute;
      bottom: -20px;
      left: -61.5%;
      width: 100%;
      height: 3px;
      background-color: #232323;
      overflow: hidden;

      @media only screen and (min-width: 320px) and (max-width: 359px) {
        left: -3.5%;
      }

      @media only screen and (min-width: 360px) and (max-width: 375px) {
        left: -12.5%;
      }

      @media only screen and (min-width: 376px) and (max-width: 420px) {
        left: -16.5%;
      }
    }
  }

  &__produtos-wrapper {
    display: flex;
    align-items: center;

    @media screen and (max-width: 992px) {
      flex-direction: column;
    }
  }

  &__produto-principal {
    width: 330px;
    height: 420px;

    @media screen and (max-width: 992px) {
      height: auto;
    }

    .itemA {
      a {
        display: block;
        border: 1px solid #eeeeee;
        width: 80%;
        margin: 25px auto;

        img {
          width: 100%;
          height: auto;
          padding: 50px;
        }
      }

      h3 {
        color: #545454;
        font-family: $thirdFont;
        font-weight: 700;
        line-height: 24px;
        text-transform: uppercase;

        a {
          border: none;
          width: 80%;
          margin: 0 auto;
          font-family: Product Sans;
          font-size: 12px;
          font-weight: 500;
        }
      }
    }
  }

  &__plus {
    color: #232323;
    font-family: $thirdFont;
    font-weight: 700;
    line-height: 48px;
    font-size: 50px;
    width: 100px;

    @media screen and (max-width: 992px) {
      text-align: center;
    }
  }

  &__produtos {
    width: 330px;
    height: 420px;
    border: 1px solid #000000;

    .slick-prev.slick-arrow {
      top: 45%;
      left: -25px;
      opacity: 1;
      width: 52px;
      height: 52px;

      @media screen and (max-width: 992px) {
        left: -20px;
      }

      &::after {
        content: "";
        background-color: #fff;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
      }

      &::before {
        width: 52px;
        height: 52px;
        background-color: rgba(138, 140, 141, .05);
        border-radius: 50%;
        color: rgba(0, 0, 0, .15);
        line-height: 52px;
        top: 0;
        left: 0;
      }
    }

    .slick-next.slick-arrow {
      top: 45%;
      right: -25px;
      opacity: 1;

      @media screen and (max-width: 992px) {
        right: -20px;
      }

      &::after {
        content: "";
        background-color: #fff;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
      }

      &::before {
        width: 52px;
        height: 52px;
        background-color: rgba(138, 140, 141, .18);
        border-radius: 50%;
        color: rgba(0, 0, 0, .15);
        line-height: 52px;
        top: 0;
        left: 0;
      }
    }

    .itemB {
      a {
        display: block;
        border: 1px solid #eeeeee;
        width: 80%;
        margin: 25px auto;

        img {
          width: 100%;
          height: auto;
          padding: 20px;
        }
      }

      h3 {
        color: #545454;
        font-family: $thirdFont;
        font-size: 12px;
        font-weight: 700;
        line-height: 24px;
        text-transform: uppercase;

        a {
          border: none;
          width: 80%;
          margin: 0 auto;
          font-family: Product Sans;
          font-size: 12px;
          font-weight: 500;
        }
      }
    }
  }

  &__equal {
    color: #232323;
    font-family: $thirdFont;
    font-weight: 700;
    line-height: 48px;
    font-size: 50px;
    width: 100px;
    text-align: right;

    @media screen and (max-width: 992px) {
      text-align: center;
    }
  }

  &__prices {
    width: 390px;
    height: 390px;
    display: flex;
    align-items: center;
    justify-content: center;

    @media screen and (max-width: 992px) {
      width: 330px;
      height: 150px;
    }

    .buy {
      text-align: center;

      & strong:nth-child(3) {
        display: block;
      }

      .comprar-junto {
        width: 213px;
        height: 49px;
        margin: 20px auto;
        background-color: #4e8809;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #ffffff;
        font-family: $thirdFont;
        font-size: 17px;
        font-weight: 700;
        text-transform: uppercase;
      }

      .more {
        display: none;
      }
    }
  }
}

.kitlook {
  .produto-esquerda {
    width: 65%;
    margin-right: 70px;
  }

  .produto-direita {
    width: 35%;
  }
}